// sass-lint:disable no-duplicate-properties
@if is_dev_mode() {
  @font-face {
    font-family: 'Glober Bold';
    src: font('glober/GloberBoldItalic.eot');
    src: font('glober/GloberBoldItalic.eot?#iefix') format('embedded-opentype'),
    font('glober/GloberBoldItalic.woff2') format('woff2'),
    font('glober/GloberBoldItalic.woff') format('woff'),
    font('glober/GloberBoldItalic.ttf') format('truetype'),
    font('glober/GloberBoldItalic.svg#GloberBoldItalic') format('svg');
    font-weight: bold;
    font-style: italic;
  }

  @font-face {
    font-family: 'Glober Regular';
    src: font('glober/GloberRegularItalic.eot');
    src: font('glober/GloberRegularItalic.eot?#iefix') format('embedded-opentype'),
    font('glober/GloberRegularItalic.woff2') format('woff2'),
    font('glober/GloberRegularItalic.woff') format('woff'),
    font('glober/GloberRegularItalic.ttf') format('truetype'),
    font('glober/GloberRegularItalic.svg#GloberRegularItalic') format('svg');
    font-style: italic;
  }

  @font-face {
    font-family: 'Glober';
    src: font('glober/GloberBold.eot');
    src: font('glober/GloberBold.eot?#iefix') format('embedded-opentype'),
    font('glober/GloberBold.woff2') format('woff2'),
    font('glober/GloberBold.woff') format('woff'),
    font('glober/GloberBold.ttf') format('truetype'),
    font('glober/GloberBold.svg#GloberBold') format('svg');
    font-weight: bold;
    font-style: normal;
  }

  @font-face {
    font-family: 'Glober SemiBold';
    src: font('glober/GloberSemiBoldItalic.eot');
    src: font('glober/GloberSemiBoldItalic.eot?#iefix') format('embedded-opentype'),
    font('glober/GloberSemiBoldItalic.woff2') format('woff2'),
    font('glober/GloberSemiBoldItalic.woff') format('woff'),
    font('glober/GloberSemiBoldItalic.ttf') format('truetype'),
    font('glober/GloberSemiBoldItalic.svg#GloberSemiBoldItalic') format('svg');
    font-weight: 600;
    font-style: italic;
  }

  @font-face {
    font-family: 'Glober';
    src: font('glober/GloberRegular.eot');
    src: font('glober/GloberRegular.eot?#iefix') format('embedded-opentype'),
    font('glober/GloberRegular.woff2') format('woff2'),
    font('glober/GloberRegular.woff') format('woff'),
    font('glober/GloberRegular.ttf') format('truetype'),
    font('glober/GloberRegular.svg#GloberRegular') format('svg');
    font-weight: normal;
    font-style: normal;
  }

  @font-face {
    font-family: 'Glober';
    src: font('glober/GloberSemiBold.eot');
    src: font('glober/GloberSemiBold.eot?#iefix') format('embedded-opentype'),
    font('glober/GloberSemiBold.woff2') format('woff2'),
    font('glober/GloberSemiBold.woff') format('woff'),
    font('glober/GloberSemiBold.ttf') format('truetype'),
    font('glober/GloberSemiBold.svg#GloberSemiBold') format('svg');
    font-weight: 600;
    font-style: normal;
  }
}
