.partner-list {
  position: relative;
  z-index: 0;
  margin-bottom: 40px;

  h1 {
    @extend .main-title;
    margin-bottom: 30px;
    font-size: 25px;
    line-height: 40px;
    color: currentColor;
  }

  .box-dark {
    &::before { // overflow background
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: -25px;
      height: 100%;
      width: 100vw;
      background-color: $lightgrey-1;
    }
  }
}

.pl {
  &-main-content {
    position: relative;
  }

  &-item {
    position: relative;
    padding: 40px 0 40px 213px;
    border-top: 1px solid $lightgrey-1;
    border-bottom: 0;
    min-height: 183px + (40px * 2);

    &::before { // partner logo background
      content: '';
      position: absolute;
      top: 40px;
      left: 0;
      display: block;
      width: 183px;
      height: 183px;
      background-color: $lightgrey-3;
      border-radius: 50%;

      .box-dark & {
        background-color: #fff;
      }
    }

    &-title {
      margin-bottom: 16px;
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      line-height: 40px;
      color: currentColor;
    }

    &-illust,
    .gla-item-img,
    .at-illust,
    .la-item-img,
    .cx-lazy-load {
      position: absolute;
      top: 40px;
      left: 0;
      max-height: 183px;
      max-width: 183px;
    }

    &-read-more {
      margin-top: 20px;
    }

    &-content {
      width: auto;
    }

    .ezxmltext-field {
      font-size: 16px;
      font-weight: 300;
      line-height: 25px;

      > p {
        line-height: inherit;
      }
    }

    .link-read-more {
      @extend %button-underline;

      &:focus,
      &:active,
      &:hover {
        background: none;
      }

      .icon {
        display: none;
      }
    }
  }

  &-trigger {
    @extend %button;

    &:focus,
    &:hover {
      color: #fff;
      outline: 0;
    }

    &-wrap {
      text-align: center;

      &::before {
        display: none;
      }
    }
  }
}
