.search-result {
  .social-sharing {
    margin: 0;
    padding: 0;
    border: 0;
  }
}

.sr {
  &-main-title {
    @extend %content-h2Like;
    text-align: left;
    border: 0;
  }

  &-title {
    .social-sharing & {
      display: inline-block;
      font-weight: 700;
      margin-right: 20px;
    }
  }

  &-term {
    color: $theme-color-2;
    background: #ff0;
    font-weight: bold;
  }

  &-nb-result {
    color: $textcolor-2;
    &:before {
      content: none;
    }
  }

  &-form {
    @include mq_min_width($tablet-medium) {
      width: percentage(850px / $wrapper-size);
    }
    margin-bottom: 40px;

    .gsf {
      &-fields {
        position: relative;
        padding: 0;
        border-radius: 5px;
        background-color: $lightgrey-3;
        overflow: hidden;
      }

      &-input {
        width: 100%;
        height: 100%;
        padding: 0 25px;
        background: none;
        border: 0;
      }

      &-submit {
        &-text {
          display: none;
        }

        &-btn {
          min-height: 64px;
          padding: 0 30px;
          color: $theme-color-2;
          background: none;

          .icon {
            display: block;
            font-size: 24px;
            color: currentColor;
            @include transition(.3s, transform);
          }

          &:hover {
            .icon {
              transform: scale(1.1);
            }
          }
        }
      }
    }
  }

  &-group {
    margin-bottom: 40px;

    &-title {
      @extend .main-title;
      margin-bottom: 40px;
    }

    .social-sharing [class^='btn'] {
      margin-right: 7px;
      padding: 0;
      font-size: 27px;
      color: $lightgrey-1;
      border: 0;

      &.sr-link-mail {
        width: 27px;
        height: 27px;
        font-size: 10px;
        color: #fff;
        background: $lightgrey-1;
        border-radius: 50%;

        .icon {
          line-height: 27px;
        }
      }

      &:focus,
      &:hover {
        color: $lightgrey-2;

        &.sr-link-mail {
          color: #fff;
          background: $lightgrey-2;
        }
      }
    }
  }

  &-item {
    .sr-group > & {
      position: relative;
      margin: 0;
      padding: 40px 0;
      border-top: 1px solid $lightgrey-1;
      border-bottom: 0;
    }

    .sr-group-exponent > & {
      display: block;
    }

    .social-sharing & {
      float: left;
      margin: 0;
      padding: 0;
      border: 0;

      .icon {
        margin: 0;
      }
    }

    &-img {
      float: none;
      margin: 0 auto 30px;
      display: block !important;
      @include mq_min_width($tablet-medium) {
        float: left;
        margin: 0 30px 0 0;
      }
      position: relative;
      width: 183px;
      height: 183px;
      padding: 0;
      text-align: center;
      overflow: hidden;

      .sr-group-exponent & {
        @include mq_min_width($tablet-medium) {
          float: left;
        }
        width: 183px;
        padding: 0;
      }

      img {
        position: absolute;
        top: 50%;
        left: 50%;
        display: block;
        max-width: none;
        min-width: 100%;
        min-height: 100%;
        object-fit: cover;
        font-family: 'object-fit: cover;';
        transform: translate(-50%, -50%);
      }
    }

    &-title {
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      color: currentColor;
      margin-bottom: 0;
      line-height: normal;
      & + .sr-item-txt {
        margin-top: 16px;
      }
    }

    &-content {
      font-size: 16px;
      font-weight: 300;
      line-height: 25px;
      color: currentColor;
      text-align: center;
      display: block !important;
      margin-left: auto;
      margin-right: auto;
      @include mq_min_width($tablet-medium) {
        text-align: left;
        margin-left: 0;
        margin-right: 0;
      }

      .sr-group-exponent & {
        width: auto;
        padding: 0;
      }
    }

    &-txt {
      .social-sharing {
        display: none;
      }
    }
  }

  &-list {
    .social-sharing & {
      display: flex;
      align-items: center;
    }
  }
}
