.list-articles.block.block-page.prix-inno {
  margin: 40px 0;
  @include mq_min_width($tablet-medium) {
    margin: 80px 0;
  }
  padding: 50px 0 40px;
  width: 100%;
  position: relative;
  @include mq_min_width($desktop-medium) {
    padding: 69px 77px 80px 0;
    width: calc(100% - 107px);
  }
  &:before {
    content: '';
    background: $theme-color-1;
    width: 100vw;
    right: 50%;
    transform: translateX(50%);
    top: 0;
    height: 100%;
    z-index: 1;
    position: absolute;
    @include mq_min_width($desktop-medium) {
      right: 0;
      transform: none;
    }
  }
  .custom_generic_progressBar {
    @if not $is_mobile {
      display: none !important;
    } @else {
      z-index: 2;
      position: relative;
      bottom: auto;
      margin-top: 30px;
    }
  }
  .prix-inno__arrows-container,
  & > .inside {
    position: relative;
    z-index: 2;
  }
  .prix-inno__arrows-container {
    @include slider-arrows;
    display: none;
    @include mq_min_width($tablet-medium) {
      display: flex;
    }
    @include mq_min_width($desktop-small) {
      display: none;
    }
  }
  .main-title-with-link {
    margin-bottom: 43px;
    border: 0;
    .main-title {
      float: none;
      text-transform: none;
      font-weight: 900;
      color: $textcolor-3;
      font-size: 30px;
      line-height: 44px;
      @include mq_min_width($desktop-small) {
        font-size: 40px;
        line-height: 60px;
      }
    }
  }
  .grid-la-list {
    margin: 0;
    align-items: stretch;
    justify-content: flex-start;
    flex-wrap: nowrap;
    .gla-item {
      border-radius: 5px;
      margin: 0;
      width: calc(100vw - 50px);
      max-width: 330px;
      transition: opacity 0.2s ease-in-out;
      &:not(.swiper-slide-visible) {
        opacity: 0.4;
        @include mq_min_width($desktop-small) {
          opacity: 1;
        }
      }
      @include mq_min_width($desktop-small) {
        max-width: none;
        width: calc((100% - 2 * 15px) / 3);
        @include mq_min_width($desktop-medium) {
          width: calc((100% - 2 * 37px) / 3);
        }
        &:not(:first-of-type) {
          margin-left: 15px;
          @include mq_min_width($desktop-medium) {
            margin-left: 37px;
          }
        }
      }
      & > a {
        border-radius: 5px;
        position: relative;
      }
      .at-illust,
      .gla-item-img,
      .la-item-img,
      .cx-lazy-load {
        height: 184px;
        width: 100%;
      }
    }
    .gla-item-content {
      padding: 22px 25px 20px;
      justify-content: flex-end;
      align-items: flex-start;
      min-height: 0;
      height: calc(100% - 184px);
      & > * {
        position: relative;
        z-index: 1;
      }
      .gla-item-tag {
        z-index: 0;
        top: calc(184px + 11px);
        right: 10.5px;
        left: auto;
        padding: 0;
        position: absolute;
        font-size: 0;
        color: transparent;
        letter-spacing: 0;
        user-select: none;
        height: 45px;
        pointer-events: none;
        text-indent: -99999px;
        overflow: hidden;
        background-color: transparent;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50% 50%;
        &.topic_grand_prix_or {
          background-image: image('visual/or.svg');
          width: 61px;
        }
        &.topic_grand_prix_argent {
          background-image: image('visual/argent.svg');
          width: 62px;
        }
        &.topic_grand_prix_bronze {
          background-image: image('visual/bronze.svg');
          width: 61px;
        }
      }
      .gla-item-title {
        font-weight: 900;
        font-family: $font-2;
        color: $textcolor-2;
        margin-bottom: 8px;
        & > span[class^='category-prix'] {
          font-size: 12px;
          font-weight: 500;
          letter-spacing: 1.04px;
          line-height: 22px;
          display: block;
          margin-bottom: 14px;
        }
      }
      .gla-item-desc {
        margin-top: auto;
        font-family: $font-1;
        line-height: 27px;
        font-size: 15px;
        font-weight: 500;
        color: $lightgrey-2;
        strong:not(:first-of-type) {
          &:before {
            content: '\A';
            white-space: pre;
          }
        }
      }
      .gla-item-btn {
        margin: 7px 0 0;
        .gla-btn-read-more {
          font-size: 0;
          letter-spacing: 0;
          color: transparent;
          &:before {
            color: $textcolor-5;
            letter-spacing: normal;
            font-size: 16px;
            html[lang='fr'] & {
              content: 'Voir plus';
            }
            html[lang='en'] & {
              content: 'See more';
            }
          }
        }
      }
    }
  }
  & + .block.block-page.prix-secteurs {
    &:after {
      @include mq_min_width($desktop-medium) {
        content: '';
        background: #353535;
        width: 200vw;
        left: 50%;
        transform: translateX(-50%);
        top: -344px;
        height: 799px;
        z-index: 0;
        position: absolute;
      }
    }
  }
}
