@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

/* Page /Infos-Pratiques/Rendez-vous-a-SIAL-Paris-du-18-au-22-Octobre-2020 */
body.node1316958 {
  @include mq_min_width($desktop-medium) {
    #zone4 .quicklinks {
      transform: translateY(-20px);
    }
  }
  .article-wrapper {

    .article {
      &-title {
        img.at-illust,
        img.gla-item-img,
        img.la-item-img,
        img.cx-lazy-load {
          display: none;
        }
      }

      &-content {
        font-size: 16px;
        margin-top: 0;
        padding-bottom: 92px;

        h3,
        h4 {
          @extend %content-h2Like;
          margin-bottom: 12px;
          margin-top: 0;
        }

        h4 {
          text-align: left;
        }

        > p {
          &:last-of-type {
            margin-bottom: 22px;
          }
          &.txtcenter {
            font-size: 20px;
          }
        }

        iframe {
          width: 100%;
          max-height: 386px;
        }

        .card {

          &-wrapper {
            display: flex;
            margin-bottom: 50px;
            flex-direction: column;
            @include mq_min_width($desktop-small) {
              flex-direction: row;
            }
          }

          width: 100%;
          max-width: 500px;
          min-height: 242px;
          border: 0;
          background-color: #fff;
          margin: 10px auto;
          display: flex;
          padding: 0;
          text-align: left;

          img {
            max-width: 203px;
            width: 50%;

            @include mq_min_width($tablet-medium) {
              width: auto;
            }
          }

          h3 {
            text-align: left;
            text-transform: lowercase;

            &:first-letter {
              text-transform: uppercase;
            }
          }

          @include mq_min_width($desktop-small) {
            width: 50%;

            &:first-child {
              margin: 10px 15px 10px auto;
            }
            &:last-child {
              margin: 10px auto 10px 15px;
            }
          }

          &:last-child {
            p:first-of-type {
              font-weight: 700;
            }
          }

          figcaption {
            padding: 35px 9px 27px 10px;

            @include mq_min_width($tablet-small) {
              padding-left: 30px;
            }
          }

          ul {
            padding: 0;
            margin: 0;

            li {
              &:nth-child(odd) {
                font-weight: 700;
              }
              &::before {
                content: none;
              }
            }
          }
        }
      }
    }
  }
}
