body.faq {
  .block-page.faq {
    padding: 20px 0 50px;
    @include mq_min_width($tablet-medium) {
      padding: 20px 15px 50px;
    }
    @include mq_min_width($desktop-small) {
      padding: 20px 106.5px 92px;
    }
    position: relative;
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      background-color: $lightgrey-3;
      width: 100vw;
      height: calc(100% + 80px);
      z-index: -1;
      top: -80px;
    }
    .faq-main-title {
      margin-bottom: 56px;
      text-align: center;
      padding: 0;
      border: 0;
      color: $textcolor-3;
      font-family: $font-2;
      font-weight: 900;
      font-size: 30px;
      line-height: 45px;
      @include mq_min_width($tablet-medium) {
        line-height: 60px;
        font-size: 42px;
      }
    }
    .fc-item-nb,
    .faq-summary,
    .faq-sub-title,
    .intro {
      display: none;
    }
    .fc-title {
      padding: 0;
      font-family: $font-2;
      font-size: 2.5rem;
      font-weight: 900;
      color: $textcolor-2;
      border: 0;
      margin-bottom: 1.5rem;
    }
    .faq-content {
      padding: 0;
    }
    .fc-item-link {
      position: relative;
      display: block;
      color: $textcolor-2;
      font-size: 20px;
      font-weight: 600;
      line-height: 31px;
      text-decoration: none;
      border-bottom: 1px solid $lightgrey-4;
      padding: 30px 90px 30px 20px;
      @include mq_min_width($tablet-medium) {
        padding: 30px 100px 30px 30px;
      }
      @include mq_min_width($desktop-small) {
        padding: 30px 179px 30px 109px;
      }
      .icon {
        display: none;
      }
      &:before,
      &:after {
        position: absolute;
        top: 22.5px;
        width: 41px;
        height: 41px;
        box-sizing: border-box;
        right: 20px;
        @include mq_min_width($tablet-medium) {
          right: 30px;
        }
        @include mq_min_width($desktop-small) {
          right: 109px;
        }
      }
      &:before {
        content: '';
        border: 1px solid $theme-color-2;
        border-radius: 50%;
        transition: background-color 0.2s ease-in-out;
      }
      &:after {
        content: '\271a';
        @include icomoon;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $theme-color-2;
        transition: color 0.2s ease-in-out;
        font-size: 15px;
      }
      &:hover,
      &:active {
        &:before {
          background: $theme-color-2;
        }
        &:after {
          color: #fff;
        }
      }
    }
    .fc-item {
      border: 0;
      padding: 0;
      background-color: #fff;
      position: relative;
      transition: box-shadow 0.2s ease-in-out;
      box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0);
      &.is-open {
        box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.08);
        .fc-item-link {
          &:after {
            content: '\2796';
          }
        }
      }
      &:not(:last-of-type) {
        margin-bottom: 7.5px;
      }
    }
    .fc-item-content {
      padding: 30px 20px 19px;
      @include mq_min_width($tablet-medium) {
        padding: 30px 30px 19px;
      }
      @include mq_min_width($desktop-small) {
        padding: 30px 109px 19px;
      }
      line-height: 31px;
      font-size: 20px;
      font-weight: 300;
    }
  }
}
