.list-articles {
  body.article_list & {
    position: relative;
    &:before {
      content: '';
      position: absolute;
      z-index: -1;
      left: 50%;
      transform: translateX(-50%);
      height: 100%;
      max-height: 640px;
      width: 100vw;
      top: -80px;
      background: $lightgrey-3;
    }
    .inside {
      position: relative;
      z-index: 1;
    }
    .main-title-with-link {
      margin: 0;
      border: 0;
      & > .main-title {
        float: none;
        text-transform: none;
        font-size: 30px;
        font-weight: 900;
        color: $textcolor-3;
        line-height: 45px;
        margin-bottom: 25px;
        @include mq_min_width($tablet-medium) {
          line-height: 60px;
          font-size: 42px;
        }
      }
    }
    .intro {
      margin: 0 auto 40px;
      font-size: 18px;
      font-weight: 300;
      line-height: 30px;
      @include mq_min_width($tablet-medium) {
        font-size: 20px;
      }
    }
    .gla-item-content p:not(.gla-item-btn) {
      font-size: 15px;
    }
    .grid-la-list {
      align-items: stretch;
      justify-content: flex-start;
      margin-left: 0;
      .gla-item {
        flex-grow: 0;
        flex-shrink: 0;
        width: 100%;
        margin-left: 0;
        @include mq_min_width(651px) {
          margin-left: 10px;
          width: calc((100% - 1 * 16px) / 2);
          &:nth-child(2n + 1) {
            margin-left: 0;
          }
        }
        @include mq_min_width(881px) {
          width: calc((100% - 2 * 16px) / 3);
          &:nth-child(2n + 1) {
            margin-left: 10px;
          }
          &:nth-child(3n + 1) {
            margin-left: 0;
          }
        }
        @include mq_min_width(1141px) {
          width: calc((100% - 3 * 16px) / 4);
          &:nth-child(2n + 1),
          &:nth-child(3n + 1) {
            margin-left: 10px;
          }
          &:nth-child(4n + 1) {
            margin-left: 0;
          }
        }
      }
    }
  }
}
