@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.footer-logo {
  margin-right: auto;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  @include mq_min_width($desktop-small) {
    margin-top: 11px;
    margin-bottom: 0;
    display: block;
    max-width: 10%;
  }
  @include mq_min_width($desktop-medium) {
    max-width: none;
  }
  &__event {
    display: block;
    @include mq_min_width($desktop-small) {
      margin-bottom: 25px;
    }
  }
  &__company {
    display: block;
    margin-left: 30px;
    @include mq_min_width($desktop-small) {
      margin-left: 0;
    }
  }
  &__img {
    width: 100%;
  }
}
