.block.quicklinks-cta--large {
  @extend .quicklinks-base;
  position: relative;
  z-index: 3;
  max-width: 1100px;
  margin: 0 auto 40px;

  @include mq_min_width($tablet-medium) {
    margin-bottom: 80px;
  }

  .ql-list {
    @include mq_min_width($tablet-medium) {
      justify-content: center;
    }
  }

  .ql-item {
    @include mq_min_width($tablet-medium) {
      min-width: 250px;
      margin-left: 20px;
    }
  }
}
