.detail-secteur {
  padding: 50px 0;
  font-family: $font-1-alt;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  @include mq_min_width($desktop-small) {
    padding: 72px 106.5px;
    margin: 28px 0;
    display: block;
    @include clearfix;
  }
  &:nth-last-child(1) {
    margin-bottom: 0;
  }
  & + *:not(.detail-secteur) {
    margin-top: 50px;
  }
  &__main {
    font-size: 16px;
    line-height: 25px;
    width: 100%;
    order: 1;
    p {
      text-align: justify;
    }
    @include mq_min_width($desktop-small) {
      width: 100%;
    }
    h2 {
      padding: 0;
      border: 0;
      font-weight: 900;
      font-size: 30px;
      font-family: $font-2;
      line-height: 44px;
      color: $textcolor-3;
      margin-bottom: 25px;
      @include mq_min_width($desktop-small) {
        font-size: 40px;
        line-height: 60px;
      }
    }
    p, .p-like, ul, ol, dl, blockquote, pre, td, th, label, textarea, caption, details, figure {
      &:not(:nth-child(1)) {
        margin-top: 25px;
      }
    }
  }
  &__additionnal {
    width: 100%;
    order: 2;
    margin-top: 30px;
    @include mq_min_width($desktop-small) {
      width: 45.37%;
      &.align-right {
        float: right;
        margin: 0 0 30px 60px;
      }
      &.align-left {
        float: left;
        margin: 0 60px 30px 0;
      }
    }
    & > ul.detail-secteur__datas + img,
    & > img + ul.detail-secteur__datas {
      margin-top: 30px;
    }
    & > img {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
  }
  ul.detail-secteur__datas {
    background: $lightgrey-3;
    color: $textcolor-4;
    margin: 0;
    padding: 15px 25px;
    @include mq_min_width($tablet-small) {
      padding: 22px 50px 24px;
    }
    & > li {
      padding-bottom: 20px;
      padding-top: 16px;
      @include mq_min_width($tablet-small) {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
      }
      p {
        font-family: $font-2;
        font-weight: 900;
        font-size: 20px;
        line-height: 32px;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        .nth-line {
          font-family: $font-1-alt;
          font-weight: 400;
          font-size: 18px;
          display: block;
          margin-top: 5px;
          line-height: 23px;
          .line {
            display: inline-block;
            position: relative;
            z-index: 1;
            &:before {
              content: '';
              position: absolute;
              z-index: -1;
              top: 0;
              left: -7.5px;
              width: calc(100% + 15px);
              transform: skewX(-25deg);
              height: 100%;
              background: $theme-color-1;
            }
            &:not(:last-of-type) {
              margin-bottom: 10px;
            }
          }
        }
      }
      &:before {
        @include icomoon;
        height: auto;
        margin: 0 0 10px;
        background: none;
        transform: none;
        flex-shrink: 0;
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        font-size: 45px;
        width: 63px;
        color: $lightgrey-1;
        @include mq_min_width($tablet-small) {
          padding-top: 9px;
          margin: 0 25px 0 0;
        }
      }
      &.custom-icon-rice {
        &:before {
          content: '\e90c';
        }
      }
      &.custom-icon-coffee {
        &:before {
          content: '\2615';
        }
      }
      &.custom-icon-tea {
        &:before {
          content: '\1f375';
        }
      }
      &.custom-icon-coins {
        &:before {
          content: '\1f4b0';
        }
      }
      &.custom-icon-increase {
        &:before {
          content: '\1f4c8';
        }
      }
      &.custom-icon-flower {
        &:before {
          content: '\1f337';
        }
      }
      &.custom-icon-megaphone {
        &:before {
          content: '\1f4e2';
        }
      }
      &.custom-icon-world {
        &:before {
          content: '\1f30e';
        }
      }
      &.custom-icon-product {
        &:before {
          content: '\1f6cd';
        }
      }
      &:not(:last-of-type) {
        position: relative;
        padding-bottom: 22px;
        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: -25px;
          height: 2px;
          width: 100%;
          background: $lightgrey-1;
          @include mq_min_width($tablet-small) {
            left: 0;
            width: 73%;
          }
        }
      }
    }
  }
  &.blue-mode {
    background-color: $lightgrey-3;
    position: relative;
    z-index: 1;
    &:before {
      content: '';
      top: 0;
      left: 50%;
      position: absolute;
      transform: translateX(-50%);
      height: 100%;
      width: 1361px;
      background-color: $lightgrey-3;
      z-index: -1;
    }
    .detail-secteur__datas {
      background-color: #fff;
      & > li {
        &:not(:last-of-type) {
          &:after {
            background: $theme-color-1;
          }
        }
      }
    }
  }
}
