@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

%commercial-card-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-flow: row wrap;
}

%commercial-card {
  width: 100%;
  display: flex;
  padding: 0;
  border-radius: 5px;
  background-color: #FFF;
  box-shadow: 0 4px 9px 0 rgba(0,0,0,0.07);

  > * {
    padding: 0;
  }
}

%commercial-card-content {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: #fff;
  border-bottom-right-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
}

%commercial-card-title {
  font-family: $font-2;
  font-weight: 900;
  color: $textcolor-2;
}

%commercial-card-social {
  padding: 0;
  margin: 0;
  border: 0;
  width: 2.7rem;
  height: 2.7rem;
  position: relative;

  a {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    border: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    user-select: none;

    .icon {
      display: none;
    }

    &:before {
      @include icomoon;
      color: $lightgrey-1;
      font-size: 2.7rem;
      transition: color 0.2s ease-in-out;
    }

    &.social-mail {
      &:before {
        content: '\2709';
      }
    }
    &.social-facebook {
      &:before {
        content: '\e900';
      }
    }
    &.social-twitter {
      &:before {
        content: '\e903';
      }
    }
    &.social-linkedin {
      &:before {
        content: '\e902';
      }
    }
  }
}
