@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.main-title {
  margin: auto;
  padding: 0;
  font-family: $font-2;
  font-size: 30px;
  font-weight: 900;
  line-height: 45px;
  color: $textcolor-3;
  border: 0;
  text-transform: none;

  @include mq_min_width($tablet-medium) {
    font-size: 42px;
    line-height: 60px;
  }

  > a {
    text-decoration: none;
  }

  &-with-link {
    .home-events & {
      border: 0;
    }

    > .main-title {
      .home-events & {
        font-family: $font-2;
        font-weight: 900;
        color: $textcolor-3;
        text-transform: none;
        font-size: 26px;
        line-height: 45px;
        @include mq_min_width($tablet-medium) {
          line-height: 60px;
          font-size: 42px;
        }
      }
    }
  }
}

%content-h2Like {
  font-family: $font-2;
  font-size: 25px;
  font-weight: 900;
  line-height: 1.6;
  text-align: center;
  color: $textcolor-2;
}
