@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.breadcrumb-nav {
  clear: both;
  width: 100%;
  margin-bottom: 22px;
  @include clearfix;

  .inside {
    display: block;
  }
  .zone > &:nth-child(1) {
    margin-top: 30px;
    body.sial-innovation & {
      @include mq_min_width($desktop-medium) {
        position: relative;
        z-index: 1;
      }
    }
  }
  a[href] {
    &:hover,
    &:active {
      text-decoration: underline;
    }
  }
}

.bn {
  &-list {
    float: none;
  }

  &-item {
    float: left;
    font-family: $font-1;
    font-size: 16px;
    font-weight: 500;
    line-height: 26px;
    color: $lightgrey-2;

    .icon {
      margin: 0 .5em 0 .33em;
      font-size: 9px;
    }

    &-link {
      display: inline-block;
      color: currentColor;
      text-decoration: none;

      .bn-item:last-child & {
        color: $theme-color-2;
      }
    }
  }
}
