@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.partner {
  position: relative;
  margin-top: 40px;
  margin-bottom: 60px;
  padding-bottom: 75px;

  @include mq_min_width($tablet-medium) {
    margin-top: 90px;
    margin-bottom: 100px;
  }

  .main-title-with-link {
    border: 0;
    text-align: center;

    > .main-title {
      float: none;
      font-family: $font-2;
      font-size: 42px;
      font-weight: 900;
      line-height: 60px;
      text-align: center;
      text-transform: none;
      color: $textcolor-3;
    }

    .link-view-all {
      @extend %button;
      float: none;
      position: absolute;
      bottom: 0;
      left: 50%;
      margin-top: 0;
      white-space: nowrap;
      transform: translateX(-50%);

      .icon {
        display: none;
      }
    }
  }

  &-gallery {
    &.slider {
      overflow: visible;
    }
  }
}

.pg {
  &-item {
    height: 120px;

    &-title {
      display: none;
    }

    &-link {
      display: block;
      filter: grayscale(1);
      @include transition(.3s, filter);

      &:hover {
        filter: grayscale(0);
      }
    }

    &-illust {
      width: auto;
      margin-bottom: 0;
      pointer-events: none;
    }
  }

  &-pagin {
    display: none;
  }
}
