.quicklinks-base {

  .ql-list {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    flex-wrap: wrap;
  }

  .ql-item {
    width: percentage(129px / 270px);
    margin-bottom: 10px;

    @include mq_min_width($tablet-medium) {
      width: percentage(250px / 1100px);
    }
  }

  .btn-primary {
    display: flex;
    width: 100%;
    height: 100%;
    padding: 20px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: $font-2;
    font-size: 15px;
    font-weight: 900;
    line-height: 22px;
    text-align: center;
    text-transform: none;
    text-decoration: none;
    color: $textcolor-2;
    background-color: #fff;
    border: 0;
    border-radius: 5px;
    box-shadow: 0 4px 9px 0 rgba(#000, .13);

    @include mq_min_width($tablet-medium) {
      flex-direction: row;
      font-size: 18px;
      line-height: 22px;
      text-align: left;
    }

    &:hover {
      .icon {
        display: inline-block;
        transform: scale(1.1);
      }
    }

    .icon {
      margin: 0 0 15px;
      @include transition(.3s, transform);

      @include mq_min_width($tablet-medium) {
        margin-right: 15px;
        margin-bottom: 0;
      }

      &::before {
        font-size: 27px;
        color: $theme-color-2;
      }

      &.icon-wallet {
        &::before { // pass icon
          color: currentColor;

          @include mq_min_width($tablet-medium) {
            font-size: 37px;
          }
        }
      }
    }
  }
}
