@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

body.parent1293828 {
  + .layout-2-col .quicklinks {
    display: none;
  }
}

body.node1316899,
body.node1316958,
body.node1316963 {
  .article-wrapper .article-content {
    margin-left: auto;
    margin-right: auto;

    @include mq_min_width($desktop-small) {
      width: 83.3334%;
    }
  }
}

/* node1316899 = Page /Infos-Pratiques/Tarifs-SIAL-Paris-2020
 * node1316958 = Page /Infos-Pratiques/Rendez-vous-a-SIAL-Paris-du-18-au-22-Octobre-2020
 * node1316963 = Page /Infos-Pratiques/Reserver-votre-hebergement-pour-SIAL-Paris-2020
 * node1316971 = Page /Infos-Pratiques/Acces-au-SIAL
 */
body.node1316899,
body.node1316958,
body.node1316963,
body.node1316971 {
  .article {
    &-wrapper {
      position: relative;

      &::after {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 50%;
        display: block;
        height: 110%;
        width: 100vw;
        background-color: $lightgrey-3;
        transform: translate(-50%, -100px);
      }

      .article-intro {
        display: block;
        color: $textcolor-2;
        font-size: 20px;
        font-weight: 300;
        line-height: 1.5;
        text-align: center;
      }
    }

    &-navigation {
      display: none;
    }
  }

  .at-main-title {
    @extend .main-title;
  }

  .quicklinks-cta--large {
    @extend .quicklinks-base;
    display: block;
    position: relative;
    margin-top: 0;

    &::before {
      content: '';
      position: absolute;
      z-index: -1;
      top: -30px;
      left: 50%;
      display: block;
      height: 60%;
      width: 100vw;
      transform: translateX(-50%);
      background-color: $lightgrey-3;
    }

    .ql-list {
      flex-direction: row-reverse;
    }

    .ql-item {
      width: 100%;

      @include mq_min_width($tablet-medium) {
        width: 40%;

        &:last-child {
          margin-left: calc(10% - 15px);
          margin-right: 15px;
        }

        &:first-child {
          margin-right: calc(10% - 15px);
          margin-left: 15px;
        }
      }
      @include mq_min_width($desktop-small) {
        width: 25%;

        &:last-child {
          margin-left: calc(25% - 15px);
          margin-right: 15px;
        }

        &:first-child {
          margin-right: calc(25% - 15px);
          margin-left: 15px;
        }
      }

      .icon-map::before {
        content: '\e90b';
      }
      .icon-home::before {
        @include icomoon;
        content: '\1f3e3';
      }
    }
  }
}

/* Page /Infos-Pratiques/Tarifs-SIAL-Paris-2020 */
body.node1316899 {
  .article {
    &-wrapper {
      &::after {
        height: 105%;
        @include mq_min_width($desktop-small) {
          height: 110%;
        }
      }

      .article-title {
        .at-main-title {
          margin-bottom: 0;
        }
      }
    }

    &-intro {
      margin-bottom: 0;

      h2 {
        @extend %content-h2Like;
        text-align: center;
        border: 0;
        margin-bottom: 0;
        margin-top: 5rem;
      }

      figure {
        margin-bottom: 0;
      }
    }

    &-content {
      > p {
        &:first-child {
          display: none;
        }

        &:nth-of-type(3),
        &:nth-of-type(4),
        &:nth-of-type(5),
        &:nth-of-type(6),
        &:nth-of-type(7) {
          font-size: 16px;
          margin: 0;
          display: inline-block;
        }

        &:nth-of-type(3) {
          margin-top: 12px;
        }

        &:nth-of-type(3),
        &:nth-of-type(5) {
          text-transform: uppercase;
          font-family: $font-2;
          font-size: 12px;
          font-weight: 900;
          letter-spacing: 1.04px;
          line-height: 3;
          width: 72px;
          margin-top: 8px;
        }

        &:nth-of-type(4),
        &:nth-of-type(6),
        &:nth-of-type(7) {
          margin-top: 12px;
          @include clearfix;
          width: calc(100% - 75px);
          float: right;
          @include mq_min_width($desktop-medium) {
            width: 89%;
            float: none;
          }
        }

        &:nth-of-type(7) {
          margin-left: 8%;
          margin-top: 0;
          margin-bottom: 2rem;
        }

        &:nth-of-type(8) {
          margin-top: 35px;
          margin-bottom: 80px;
        }

        &:nth-last-child(-n+2) {
          display: none;
        }
      }

      a {
        text-decoration: none;
      }

      h2 {
        @extend %content-h2Like;
        text-align: center;
        border: 0;
        margin-bottom: 8px;
        margin-top: 0;

        + p {
          text-align: center;
          font-size: 16px;
          font-weight: 300;
          line-height: 1.625;
        }

        + figure {
          padding: 0;
          margin: 0;
          border: 0;
          margin-bottom: 52px;

          img {
            width: 100%;

            @include mq_min_width($desktop-small) {
              max-height: 186px;
            }
          }
        }

        &:last-of-type {
          text-align: left;
          margin-bottom: 0;

          strong {
            font-weight: 900;
          }

          + p {
            text-align: left;
            margin-top: 0;
          }
        }
      }

      ul {
        padding-left: 10px;
        margin-bottom: 50px;

        li {
          font-size: 16px;
          list-style: disc;

          &::before {
            content: none;
          }
        }
      }

      table {
        position: relative;
        margin-bottom: 40px;
        width: 100%;
        display: block !important;
        border-top: 1px solid rgba($lightgrey-2, 0.27);
        border-right: 1px solid rgba($lightgrey-2, 0.27);
        box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.13);
        @include mq_min_width($desktop-small) {
          display: table !important;
          border: 0;
          box-shadow: none;
        }
        tbody {
          display: block;
          width: 100%;
          @include mq_min_width($desktop-small) {
            display: table-row-group;
            width: auto;
          }
        }

        tr {
          background: none;
          display: flex;
          align-items: flex-start;
          justify-content: center;
          width: 100%;
          position: relative;
          @include mq_min_width($desktop-small) {
            display: table-row;
            width: auto;
            position: static;
          }
          &:not(:first-child) td:after {
            position: absolute;
            font-size: 11px;
            top: 0;
            left: 5px;
            z-index: 2;
            color: $textcolor-3;
            font-family: $font-2;
            font-weight: 900;
          }
          &:nth-child(2) td::after {
            content: 'Individuel';
            @include mq_min_width($desktop-small) {
              content: none;
            }
          }
          &:nth-child(3) td::after {
            content: 'Trio';
            @include mq_min_width($desktop-small) {
              content: none;
            }
          }
          &:nth-child(4) td::after {
            content: 'Groupe (1)';
            @include mq_min_width($desktop-small) {
              content: none;
            }
          }
          &:nth-child(5) td::after {
            content: 'Ecole (2)';
            @include mq_min_width($desktop-small) {
              content: none;
            }
          }

          td {
            @extend %content-h2Like;
            border: 0;
            line-height: 2;
            margin: 0;
            padding: 0;
            width: 25%;
            position: relative;
            display: block;
            vertical-align: middle;
            border-left: 1px solid rgba($lightgrey-2, 0.27);
            border-bottom: 1px solid rgba($lightgrey-2, 0.27);
            @include mq_min_width($desktop-small) {
              width: 20%;
              border: 0;
              vertical-align: top;
              display: table-cell;
              position: static;
              height: 69px;
            }
            &:not(:first-child) {
              p {
                line-height: 2;
                font-size: 16px;
                font-family: $font-2;
                font-weight: 900;
                background-color: #FFF;
                padding: 15px;
                height: 69px;
                display: flex;
                align-items: center;
                justify-content: center;
                @include mq_min_width($desktop-small) {
                  font-size: 20px;
                  width: 90%;
                  max-width: 200px;
                  padding: 18px 10px;
                  border-bottom: 1px solid rgba($lightgrey-2, 0.27);
                  box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.13);
                  margin-left: auto;
                  margin-right: 0;
                }
              }
            }

            &:first-child {
              display: none;
              @include mq_min_width($desktop-small) {
                display: table-cell;
              }
              color: $textcolor-3;
              font-size: 12px;
              letter-spacing: 1.04px;
              line-height: 3;
              background: none;
              text-align: left;
              vertical-align: inherit !important;
              position: relative;
              padding-left: 0;
              padding-right: 50px;
              width: 8.3334%;

              p {
                background: none;
                margin-right: -10px;
              }

              &::before {
                content: '';
                display: block;
                height: 1px;
                width: calc(100% + (100% / 3));
                position: absolute;
                background-color: $lightgrey-2;
                opacity: 0.27;
                bottom: 0;
                right: calc((15px + (100% / 5)) * -1);
              }
            }

            p {
              position: relative;
              z-index: 2;
            }
          }

          &:last-child,
          &:first-child {
            @include mq_min_width($desktop-small) {
              td,
              td p {
                border: 0;
              }
            }

            td::before {
              content: none;
            }
          }

          &:first-child {
            td {
              color: #FFF;

              p {
                height: 85px;
              }

              &:nth-of-type(2) p {
                background-color: $theme-color-1;
              }

              &:nth-of-type(3) p {
                word-break: break-word;
                background-color: #FFBC38;
              }

              &:nth-of-type(4) p {
                background-color: #FF9038;
              }

              &:nth-of-type(5) p {
                background-color: $theme-color-2;
              }
            }
          }
        }

        &::after {
          position: absolute;
          bottom: -40px;
          content: '';
          display: block;
          height: 1px;
          width: 100%;
          left: 0;
          background-color: $lightgrey-2;
          opacity: 0.27;
        }
      }

      .lien1 {
        @extend %button;
        margin-top: 0;
      }
    }
  }

  .article-navigation {
    display: none;
  }

  .at-main-title {
    @extend .main-title;
  }

  .quicklinks-cta--large {
    margin-bottom: 40px;
    margin-top: -1.5rem;

    @include mq_min_width($tablet-medium) {
      margin-top: 0 !important;
    }

    .ql-list {
      @include mq_min_width($tablet-medium) {
        justify-content: center;
        flex-wrap: nowrap;
      }
    }
  }
}
