.partner-of-the-event {
  margin: 40px 0;
  position: relative;
  padding: 50px 0 40px;
  @include mq_min_width($desktop-medium) {
    padding: 80px 114px 80px 0;
  }
  @include mq_min_width($tablet-medium) {
    margin: 80px 0;
  }
  & > * {
    position: relative;
    z-index: 2;
  }
  &:before {
    content: '';
    background: $lightgrey-3;
    width: 100vw;
    top: 0;
    height: 100%;
    z-index: 1;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    @include mq_min_width($desktop-medium) {
      left: auto;
      right: 0;
      transform: none;
    }
  }
  .main-title {
    margin-bottom: 28.5px;
    border-bottom: 1px solid $lightgrey-1;
    padding-bottom: 15px;
    @include mq_min_width($desktop-small) {
      padding-bottom: 24px;
      margin-bottom: 37px;
    }
  }
  &__partner {
    @include mq_min_width($tablet-medium) {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
    }
    img {
      display: block;
      flex-shrink: 0;
      flex-grow: 0;
      width: 183px;
      height: 183px;
      object-fit: cover;
      font-family: 'object-fit: cover;';
      border-radius: 50%;
      overflow: hidden;
      margin: 0 auto 20px;
      @include mq_min_width($tablet-medium) {
        margin: 0 50px 0 0;
      }
    }
    &__content {
      flex: 1;
      font-size: 20px;
      line-height: 30px;
      p, .p-like, ul, ol, dl, blockquote, pre, td, th, label, textarea, caption, details, figure {
        line-height: 30px;
        margin-top: 12px;
      }
      .gla-btn-read-more {
        margin-top: 22px;
      }
    }
    &__name {
      color: $textcolor-2;
      font-weight: 900;
      font-size: 25px;
      line-height: 40px;
      font-family: $font-2;
      letter-spacing: normal;
      margin: 0;
      &:not(:nth-last-child(1)) {
        margin-bottom: 20px;
      }
    }
  }
}
