.block.article-title {
  // sass-lint:disable no-invalid-hex
  @at-root body.page-rubric #zone4 .block.block-page.edito.edito-custom, /* hub exposant (milieu de page) */
  body.front &, /* homepage */
  body.page-rubric #zone2 > .article-wrapper > &, /* hub exposant (haut de page) */
  body.page-rubric #zone4 > & /* hub visiteur */ {
    // sass-lint:enable no-invalid-hex
    margin-left: auto;
    margin-right: 0;
    margin-top: 0;
    .article-intro {
      font-size: 20px;
      font-weight: 300;
      margin: 0;
      .ezxmltext-field {
        @include clearfix;
      }
      p, .p-like, ul, ol, dl, blockquote, pre, td, th, label, textarea, caption, details, figure {
        line-height: 30px;
        clear: both;
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
      .lien1 {
        @extend %button;
        clear: both;
      }
      .focus2 {
        clear: both;
        float: left;
        width: 100%;
        max-width: none;
        border-radius: 5px;
        background-color: #FFF;
        height: 182px;
        box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.13);
        transition: transform 0.2s ease-in-out;
        @include mq_min_width($tablet-small) {
          max-width: 183px;
        }
        @include mq_min_width($tablet-medium) {
          clear: left;
          width: calc((100% - 2 * 30px) / 3);
        }
        &:hover,
        &:active {
          transform: translateY(-5px);
        }
        &:not(:first-child) {
          margin-top: 30px;
          @include mq_min_width($tablet-medium) {
            margin-top: 50px;
          }
        }
        a {
          display: flex;
          width: 100%;
          height: 100%;
          padding: 34px 75px 34px 25px;
          text-decoration: none;
          font-family: $font-2;
          font-size: 25px;
          font-weight: 900;
          line-height: 35px;
          position: relative;
          overflow: hidden;
          user-select: none;
          z-index: 1;
          &:before {
            @include icomoon;
            content: '\1f4cd';
            color: #E9EBEF;
            z-index: -1;
            position: absolute;
            font-size: 145px;
            bottom: -21px;
            right: -6px;
          }
        }
        & + .focus2 {
          @include mq_min_width($tablet-medium) {
            clear: none;
            margin-left: 30px;
          }
          a {
            &:before {
              content: '\1f4f1';
              font-size: 139px;
              bottom: 0;
              right: 16px;
            }
          }
          & + .focus2 {
            a {
              &:before {
                content: '\1f4ac';
                font-size: 136px;
                bottom: 11px;
                right: 0;
              }
            }
          }
        }
      }
    }

    &::after {
      display: none;
    }

    & > .inside {
      @include mq_min_width($desktop-medium) {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
      }
      .at-illust,
      .gla-item-img,
      .la-item-img,
      .cx-lazy-load {
        object-fit: cover;
        font-family: 'object-fit: cover;';
        float: none;
        margin: 0 auto 40px;
        width: auto;
        max-width: 100%;
        max-height: 400px;
        @include mq_min_width($desktop-medium) {
          margin: 0;
          width: 503px;
          flex-shrink: 0;
          max-width: none;
          max-height: none;
        }
      }
      .at-content {
        width: 100%;
        margin: 0;
        padding-bottom: 30px;
        @include mq_min_width($desktop-medium) {
          margin-left: 60px;
          padding: 28px 0 0;
        }
      }
      @if $is_mobile {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        .at-illust,
        .gla-item-img,
        .la-item-img,
        .cx-lazy-load {
          order: 2;
          display: block;
          margin: 15px auto;
        }
        .at-content {
          order: 1;
        }
      }
    }
  }

  body.front .block.article-title { /* homepage */
    max-width: 1175px;
  }

  // sass-lint:disable no-invalid-hex
  @at-root body.page-rubric #zone4 .block.block-page.edito.edito-custom /* hub exposant (milieu de page) */ {
    // sass-lint:enable no-invalid-hex
    margin-top: 30px;
    margin-bottom: 30px;
    @include mq_min_width($tablet-small) {
      margin-top: 40px;
      margin-bottom: 40px;
    }

    @include mq_min_width($tablet-medium) {
      margin-top: 60px;
      margin-bottom: 60px;
    }

    @include mq_min_width($desktop-small) {
      margin-top: 80px;
      margin-bottom: 80px;
    }

    @include mq_min_width($desktop-medium) {
      margin-top: 100px;
      margin-bottom: 100px;
    }

    @if $is_mobile {
      & > .inside .at-illust,
      & > .inside .gla-item-img,
      & > .inside .la-item-img,
      & > .inside .cx-lazy-load {
        margin-top: 30px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  /* Homepage T2 bas de page */
  @at-root body.front .exhibitors-slider + .block.edito {
    margin: 40px 0;
    position: relative;
    z-index: 1;
    padding: 40px 20px;
    background: $theme-color-1;

    @include mq_min_width($tablet-medium) {
      margin: 80px 0;
      padding: 68px 20px 67px;
    }

    @include mq_min_width($desktop-small) {
      background: transparent;
      padding: 68px 0 67px 106px;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 100vw;
        z-index: -2;
        background: $theme-color-1;
      }
    }

    &:after {
      @include icomoon;
      z-index: -1;
      content: '\1f4d3';
      position: absolute;
      right: 20px;
      bottom: 0;
      font-size: 245px;
      color: $theme-color-1-dark;
      @include mq_min_width($tablet-medium) {
        font-size: 325px;
        right: 75px;
      }
    }

    .inside {
      max-width: 601px;
    }

    h2 {
      border: 0;
      padding: 0;
      margin: 0 0 30px;
      font-weight: 900;
      color: $textcolor-2;
      font-family: $font-2;
      font-size: 24px;
      line-height: 37px;
      @include mq_min_width($tablet-medium) {
        font-size: 30px;
        line-height: 45px;
      }
    }

    .lien1 {
      @extend %button;
    }
  }
}
