.list-articles.block.block-page.sial-inno {
  margin: 40px 0;
  padding: 50px 0 40px;
  position: relative;
  @include mq_min_width($tablet-small) {
    padding: 0;
  }
  @include mq_min_width($tablet-medium) {
    margin: 80px 0;
    padding: 50px 0 40px;
  }
  @include mq_min_width(1400px) {
    margin-left: -40px;
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    width: 100vw;
    height: 100%;
    left: 40px;
    top: 0;
    background: $lightgrey-3;
    display: block;
    @include mq_min_width($tablet-small) {
      display: none;
    }
    @include mq_min_width($tablet-medium) {
      display: block;
    }
  }
  & > .inside {
    position: relative;
    z-index: 2;
  }
  .grid-la-list {
    margin: 0;
    align-items: stretch;
    @if $is_mobile {
      flex-direction: column;
    }
    .gla-item {
      margin: 0 auto;
      border-radius: 6px;
      box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.13);
      width: 100%;
      max-width: 397px;
      &:nth-child(3) {
        @include mq_min_width($tablet-medium) {
          display: none;
        }
        @include mq_min_width($desktop-small) {
          display: block;
        }
      }
      @include mq_min_width($tablet-medium) {
        margin: 0;
        max-width: none;
        width: calc((100% - 1 * 15px) / 2);
      }
      @include mq_min_width($desktop-small) {
        width: calc((100% - 2 * 15px) / 3);
      }
      @include mq_min_width($desktop-medium) {
        width: calc((100% - 2 * 49px) / 3);
      }
      &:not(:first-of-type) {
        margin-top: 15px;
        @include mq_min_width($tablet-medium) {
          margin-left: 15px;
          margin-top: 0;
        }
        @include mq_min_width($desktop-medium) {
          margin-left: 49px;
        }
      }



      & > a {
        border-radius: 6px;
        position: relative;
        .at-illust,
        .gla-item-img,
        .la-item-img,
        .cx-lazy-load {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 100%;
          height: 100%;
          z-index: 1;
        }
        .gla-item-content {
          padding: 197px 15px 43px;
          background: linear-gradient(180deg, rgba(36, 34, 29, 0) 0%, rgba(35, 31, 27, 0.66) 100%);
          z-index: 1;
          color: #fff;
          align-items: flex-start;
          justify-content: flex-end;
          @include mq_min_width($desktop-medium) {
            padding: 197px 30px 43px;
          }
          .gla-item-tag {
            position: relative;
            top: auto;
            left: auto;
            background: none;
            padding: 0;
            color: $textcolor-4;
            font-size: 12px;
            font-weight: 500;
            text-transform: none;
            font-family: $font-3;
            letter-spacing: 0.89px;
            line-height: 23px;
            margin-left: 12px;
            & ~ .gla-item-title {
              margin-top: 20px;
            }
            &:before {
              content: '';
              position: absolute;
              background: #78CFE1;
              transform: translate(-50%, -50%) skewX(-25deg);
              top: 50%;
              left: 50%;
              width: calc(100% + 14px);
              height: 100%;
              z-index: -1;
            }
          }
          .gla-item-btn {
            display: none;
          }
          .gla-item-title {
            color: #fff;
            font-family: $font-2;
            font-size: 24px;
            font-weight: 900;
            line-height: 32px;
            & + .gla-item-desc {
              margin-top: 10px;
            }
          }
          .gla-item-desc {
            line-height: 28px;
            font-size: 18px;
            font-weight: 300;
          }
        }
        &:hover,
        &:focus,
        &:active {
          .at-illust,
          .gla-item-img,
          .la-item-img,
          .cx-lazy-load {
            transform: translate(-50%, -50%) scale(1.2);
          }
        }
      }
    }
  }
}
