.pagination {
  padding: 0;
  border: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 26px;
  margin: 40px 0;
  @include mq_min_width($tablet-medium) {
    margin: 50px 0 60px;
  }
  @include mq_min_width($desktop-small) {
    margin: 60px 0 80px;
  }
  @include mq_min_width($desktop-medium) {
    margin: 70px 0 100px;
  }
  .pagin-item {
    margin: 0;
    &:not(:first-of-type) {
      margin-left: 5px;
    }
    &:not(:last-of-type) {
      margin-right: 5px;
    }
    &.pagin-prev a:after,
    &.pagin-next a:after,
    a {
      color: $lightgrey-2;
      transition: color 0.3s ease-in-out;
    }
    a {
      padding: 0;
      text-decoration: none;
      background: transparent !important;
    }
    &:hover,
    &:active,
    &.is-active {
      &.pagin-prev a:after,
      &.pagin-next a:after,
      a {
        color: $theme-color-2;
      }
    }
    &.is-active {
      font-weight: 600;
    }
    &.pagin-prev,
    &.pagin-next {
      a {
        font-size: 0;
        color: transparent;
        letter-spacing: 0;
        line-height: 0;
        .icon-catal {
          display: none;
        }
        &:after {
          @include icomoon;
          content: '\1f51d';
          letter-spacing: normal;
          font-size: 12px;
          line-height: 26px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
    &.pagin-prev {
      margin-right: 20px;
      a {
        &:after {
          transform: rotate(-90deg);
        }
      }
    }
    &.pagin-next {
      margin-left: 20px;
      a {
        &:after {
          transform: rotate(90deg);
        }
      }
    }
  }
}
