@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

%displayNone {
  display: none;
}

body.node126547.exposant {
  &.body-mobile .mod-catal.catal-ex-details .edito .inside {
    @extend %displayNone;
  }
  .mod-catal {
    &.pagination {
      @extend %displayNone;
    }

    &.block.edito {
      padding: 15px 0 0;
      position: relative;

      @include mq_min_width($tablet-medium) {
        padding: 65px 8.3334% 0 0;
        background-color: $lightgrey-3;
      }
      @include mq_min_width($desktop-small) {
        padding-left: 8.3334%;
      }

      &::before {
        content: '';
        display: block;
        height: 100%;
        width: 100vw;
        background-color: $lightgrey-3;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: -1;

        @include mq_min_width($tablet-medium) {
          right: 0;
          width: 40%;
          max-width: 508px;
          left: initial;
          transform: initial;
          background-color: $theme-color-1;
          z-index: initial;
        }

        @include mq_min_width($desktop-medium) {
          width: 45%;
        }
      }

      & > h1 {
        text-align: left;
      }
    }

    &.catal-nav-ex {
      @extend %displayNone;
    }
    .catal {
      &-ed-partners,
      &-ed-activity-group:nth-of-type(3),
      &-ed-group-title,
      &-ed-main-stand-name,
      &-ed-main-stand-title,
      &-ed-main-cat-link img,
      &-ed-main-cat-more,
      &-ed-main-desc {
        @extend %displayNone;
      }

      &-ed-group {
        border: 0;
      }

      &-ed-title {
        @extend .main-title;
        text-align: center;
        width: 100%;
        margin-bottom: 37px;

        @include mq_min_width($tablet-medium) {
          width: 90%;
        }
      }

      &-ed-activity {
        .catal-ed-activity-item {
          a {
            @extend %button-underline;
            background: none;
            border-radius: 0;

            &:hover {
              background: none;
            }
          }
        }

        .catal-ed-activity-link {
          @extend %button;
          width: 100%;
        }

        &-group {
          display: flex;
          border: 0;
          flex-wrap: wrap;
          margin-bottom: 80px;
          padding: 1em 0;

          @include mq_min_width($tablet-medium) {
            flex-flow: column nowrap;
            padding: 70px 8.3334% 80px;
          }

          &:nth-of-type(4) {
            position: relative;

            &::before {
              content: '';
              display: block;
              width: 100vw;
              height: 100%;
              position: absolute;
              top: 0;
              left: 50%;
              transform: translateX(-50%);
              background-color: $lightgrey-3;
              z-index: -1;

              @include mq_min_width($tablet-medium) {
                width: 100%;
              }
            }

            .catal-ed-activity-item a {
              text-transform: lowercase;

              &:first-letter {
                text-transform: uppercase;
              }
            }
          }

          h4 {
            display: none;
          }

          ul {
            margin-left: auto;
            margin-right: auto;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            flex-flow: row wrap;
            position: relative;
            overflow: initial;

            > li {
              margin: 10px 43px 10px 0;

              &.catal-ed-activity-item-view-all {
                margin: 10px auto;
                position: absolute;
                bottom: -60px;
                left: calc(49% - (192px / 2));

                @include mq_min_width($tablet-medium) {
                  bottom: -80px;
                }

                i {
                  display: none;
                }
              }
            }
          }
        }
      }

      &-ed-products {
        .catal-edp-item {
          display: flex;
          flex-direction: column;
          margin-bottom: 25px;
          width: 260px;
          margin-left: auto;
          margin-right: auto;
          position: relative;

          @include mq_min_width($tablet-medium) {
            flex-direction: row;
            width: auto;
          }

          img {
            width: 260px;
            height: 136px;
          }
        }
        .catal-edp-title {
          border: 0;

          a {
            color: $textcolor-2;
            font-family: $font-2;
            font-size: 18px;
            font-weight: 900;
            line-height: 1.55;
            display: block;

            &:hover {
              color: $textcolor-2;
              text-decoration: none;

              &::before {
                width: 100px;
              }
            }

            &::before {
              content: '';
              position: absolute;
              bottom: calc(50% + 2px);
              left: 0;
              width: 35px;
              max-width: 100%;
              height: 1px;
              background-color: $theme-color-2;
              @include transition(.3s, width);

              @include mq_min_width($tablet-medium) {
                bottom: calc(20% + 2px);
                left: 40px;
              }
            }

            &::after {
              width: 100px;
              display: inline-block;
              padding: 2px 0;
              font-family: $font-1;
              font-size: 16px;
              font-weight: 500;
              line-height: 26px;
              text-align: left;
              text-transform: none;
              text-decoration: none;
              color: currentColor;
              background: none;
              border: 0;
              transition: none;
              overflow: hidden;
              content: 'Voir le produit';
              position: absolute;
              left: 0;
              bottom: 50%;

              @include mq_min_width($tablet-medium) {
                bottom: 20%;
                left: 40px;
              }
            }
          }
        }

        .catal-edp-title,
        .catal-edp-desc {
          width: 100%;

          @include mq_min_width($tablet-medium) {
            width: 50%;
          }
        }

        .catal-edp-cat {
          width: 100%;
          height: 75%;
          color: $textcolor-2;
          font-family: $font-2;
          font-size: 12px;
          font-weight: 500;
          letter-spacing: 1.04px;
          line-height: 3.08;
          border: 0;
          padding-left: 0;
          margin-top: 20px;

          @include mq_min_width($tablet-medium) {
            width: 50%;
            padding-left: 25px;
            position: absolute;
            top: 0;
            right: 0;
            border-left: 1px solid #e3e9ea;
            margin-top: 0;
          }

          .catal-edp-cat-item {
            text-transform: uppercase;

            .icon-catal {
              color: currentColor;
            }
          }
        }

        .catal-edp-content {
          padding: 5px 0 31px;
          margin-bottom: 0;
          flex-grow: 1;
          position: relative;
          flex-direction: column;

          @include mq_min_width($tablet-medium) {
            padding: 5px 40px 31px;
            flex-direction: row;
          }
        }
      }

      &-ed-main {
        position: relative;
        background-color: $lightgrey-3;
        border: 0;
        display: flex;
        flex-flow: column nowrap;
        @include clearfix;

        @include mq_min_width($tablet-medium) {
          padding-right: 40%;
        }

        @include mq_min_width($desktop-small) {
          padding: 0 46% 66px 8.3334%;
        }

        &-stand,
        &-cat {
          width: 100%;
        }

        &-address {
          float: none;
        }

        &-cat {
          order: 2;
          display: flex;
          align-items: flex-start;
          justify-content: flex-start;
          flex-direction: column;
          border: 0;
          margin-left: 0;

          @include mq_min_width($desktop-small) {
            flex-direction: row;
          }

          &-link {
            text-decoration: none;
            padding: 0 2em 0 0;
          }

          h2 {
            margin-left: 0;
            margin-bottom: 1.7rem;

            @include mq_min_width($desktop-small) {
              margin-bottom: 3.7rem;
            }
          }

          h2,
          &-link {
            font-family: $font-2;
            font-size: 12px;
            font-weight: 500;
            letter-spacing: 1.04px;
            line-height: 2.08;
            text-transform: uppercase;
            text-align: left;
            color: $textcolor-2;
          }

          &-item {
            max-width: initial;
            margin-bottom: 5px;
            text-align: left;
            flex: initial;
            border: 0;
          }

          h2,
          &-list {
            margin-top: 0;
          }
        }

        &::before {
          content: '';
          display: block;
          height: 100%;
          width: 100vw;
          background-color: $lightgrey-3;
          position: absolute;
          top: 0;
          left: 50%;
          transform: translateX(-50%);
          z-index: -1;

          @include mq_min_width($tablet-medium) {
            right: 0;
            width: 40%;
            max-width: 508px;
            background-color: $theme-color-1;
            left: initial;
            transform: initial;
            z-index: initial;
          }

          @include mq_min_width($desktop-medium) {
            width: 45%;
          }
        }

        img.cx-lazy-load {
          border-radius: 50%;
          height: 244px;
          width: 244px;
          margin-bottom:20px;

          @include mq_min_width($tablet-medium) {
            position: absolute;
            top: 0;
            right: calc(20% - 122px);
          }
        }

        &-url {
          float: none;
          margin-top: 15px;
        }

        &-url-link {
          @extend %button-underline;

          span {
            text-decoration: none;
          }

          .icon-catal-externe {
            @extend %displayNone;
          }
        }

        &-stand {
          background: none;
          margin: 0;
          padding: 0;
          order: 1;
        }

        &-infos {
          order: 3;

          p {
            font-size: 16px;
            line-height: 1.56;
            margin-left: 0;
          }
        }

        &-stand-ref {
          padding: 0 20px;
          color: #FFFFFF;
          font-family: $font-3;
          font-size: 12px;
          font-weight: 600;
          letter-spacing: 0.89px;
          line-height: 2.16;
          text-align: left;
          position: relative;
          z-index: 2;
          margin-left: 20px;

          &::before {
            content: '';
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            width: 100px;
            -webkit-transform: skewX(-25deg);
            -ms-transform: skewX(-25deg);
            transform: skewX(-25deg);
            height: 23px;
            background-color: $textcolor-2;
          }

          .icon-catal-localisation {
            position: absolute;
            top: 1px;
            left: -20px;

            &:before {
              @include icomoon;
              font-size: 20px;
              color: $theme-color-2;
              content: '\1f4cd';
            }
          }
        }

        &-social {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          position: absolute;
          z-index: 10;
          top: 280px;
          left: 160px;
          margin-top: 0;

          @include mq_min_width($tablet-medium) {
            top: 16px;
          }
          @include mq_min_width($desktop-small) {
            left: 22%;
            top: 0;
          }

          &-link {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            border: 0;
            padding: 0;
            width: 31px;
            height: 31px;
            background-color: #ffffff;
            position: relative;
            margin-right: 10px;

            &::before {
              @include icomoon;
              font-size: 31px;
              content: '';
              display: block;
              position: absolute;
              top: calc(50% - (31px / 2));
              left: calc(50% - (31px / 2));
              color: $lightgrey-1;
            }

            &:first-child::before {
              content: '\e900';
            }

            &:nth-child(2)::before {
              content: '\e903';
            }

            img {
              @extend %displayNone;
            }
          }
        }

      }
      &-ex-item-cat-more {
        display: none;
      }
    }
    &.catal-other-ex {

      h2 {
        @extend .main-title;
        padding-bottom: 42.5px;
        margin-bottom: 28.5px;
        margin-top: 70px;
        border-bottom: 1px solid $lightgrey-4;
        display: flex;
        align-items: center;
      }

      .catal-other-ex-link {
        @extend .gla-item;
        overflow: hidden;
        border-radius: 10px;
        box-shadow: 0 0.4rem 0.9rem 0 rgba(0, 0, 0, 0.13);
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        padding: 10px;
        text-align: center;
        text-decoration: none;

        &:hover,
        &:active {
          + .catal-other-ex-actions {
            display: none;
          }
        }

        .cx-lazy-load {
          min-height: 13rem;
          max-height: 13rem;
        }
      }

      .catal-other-ex-list {
        margin-bottom: 80px;
        flex-wrap: nowrap;
        display: flex;
        width: 100%;
        padding-left: 0;
      }

      .exhibitors-slider__btn__container {
        @include mq_min_width($tablet-medium) {
          margin: 0 0 0 auto;
        }
      }
      .catal-other-ex-media {
        img {
          width: 100%;
        }
      }

      .catal-other-ex-item {
        border: 0;
        margin: 0 15px;
        min-width: 262px;

        &:first-child {
          margin-left: 0;
        }
        &:last-child {
          margin-right: 0;
        }
      }

      .catal-other-ex-txt {
        padding: 20px;
        font-size: 16px;
        font-weight: 300;
        line-height: 1.625;
        text-align: left;
      }

      .catal-other-ex-actions  {
        display: none;
      }
    }
  }
}
