@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.subheader-navigation {
  position: relative;
  z-index: 0;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 42px;
  align-items: center;
  justify-content: space-between;
  @include clearfix;

  @if $is_mobile {
    justify-content: center;
  }

  @include mq_min_width($tablet-medium) {
    background: $lightgrey-3;
  }

  &::before { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 50%;
    display: none;
    width: 100vw;
    height: 100%;
    background: $lightgrey-3;
    transform: translateX(-50%);

    @include mq_min_width($tablet-medium) {
      display: block;
    }
  }

  .block.quicklinks-navigation {
    margin: 12px auto 15px;
    @if $is_mobile {
      margin: 30px;
    }

    @include mq_min_width($desktop-small) {
      float: left;
      margin: 0;
    }

    .ql-list {
      @if $is_mobile {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
      }
    }

    .ql-item {
      float: left;
      margin-right: 44px;
      border: 0;

      @if $is_mobile {
        margin-left: 15px;
        margin-right: 15px;
      } @else {
        &:last-child {
          margin-right: 0;
        }
      }



      &-link {
        @extend %button-underline;
        display: flex;
        align-items: center;

        @include mq_min_width($desktop-small) {
          height: 84px;
        }

        &::after { // underline
          width: 0;
          left: 50%;
          transform: translateX(-50%);
        }

        &:hover {
          color: currentColor;
          background: none;
        }
      }
    }
  }
}
