@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.subheader {
  position: relative;
  z-index: 0;
  width: 100vw;
  left: 50%;
  margin-bottom: 134px;
  transform: translateX(-50%);

  @include mq_min_width($tablet-medium) {
    margin-bottom: 0;
    overflow: hidden;
  }

  &::after { // gradient background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(270deg, rgba(#000, 0) 0%, rgba(#000, .54) 100%);
  }

  &__wrapper {
    @extend .wrapper;
    z-index: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 385px;
    margin: auto;
    align-items: center;

    @include mq_min_width($tablet-medium) {
      height: 512px;
      max-height: calc(100vh - 104px); // viewport.height - header.height
      flex-direction: row;
    }
  }

  &__content {
    margin-top: 75px;
    padding: 15px;
    text-align: center;

    @include mq_min_width($tablet-medium) {
      margin-top: 0;
      text-align: left;
    }
  }

  &__title {
    @extend .main-title;
    line-height: 63px;
    color: #fff;
  }

  &__img {
    position: absolute;
    top: 0;
    left: 50%;
    display: block;
    width: 100vw;
    height: 100%;
    text-align: center;
    overflow: hidden;
    transform: translateX(-50%);
    z-index: -1;
    background: center center / cover no-repeat;

    > * {
      position: absolute;
      top: 50%;
      left: 50%;
      right: 0;
      display: none;
      //display: block;
      max-width: none;
      min-width: 100%;
      min-height: 100%;
      margin: 0 auto;
      transform: translate(-50%, -50%);
    }
  }

  &__btn {
    @extend %button-invert;
    margin-top: 32px;
  }

  &__article {
    position: absolute;
    bottom: -114px;
    width: 280px;
    margin-left: auto;
    margin-right: auto;
    padding: 40px 30px;
    text-align: center;
    justify-self: flex-end;
    background: $theme-color-1 image('visual/idea-bg.svg') right bottom /contain no-repeat;

    @include mq_min_width($tablet-medium) {
      position: relative;
      bottom: 0;
      margin-right: 0;
      padding: 40px 10px 40px 40px;
      text-align: left;
      align-self: flex-end;
    }

    &__title {
      margin-bottom: 4px;
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      line-height: 40px;
    }

    &__content {
      line-height: 22px;

      > p {
        display: none;
        line-height: inherit;

        @include mq_min_width($tablet-medium) {
          display: block;
        }

        &:first-child {
          margin-top: 0;
        }

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &__btn {
      @extend %button;
      margin-top: 22px;
    }
  }
}
