body.faq.article_list {
  #zone4 .more-infos-contact {
    display: none;
  }
  .block-page.list-articles {
    padding: 20px 0 30px;
    margin-bottom: 40px;
    @include mq_min_width($tablet-medium) {
      padding: 20px 0 50px;
      margin-bottom: 80px;
    }
    &:before {
      max-height: none;
      height: calc(100% + 80px);
      top: -80px;
    }
    .intro {
      display: none;
    }
    .grid-la-list {
      margin: 20px 0 0;
      .gla-item {
        margin-bottom: 30px;
        width: 100%;
        margin-left: 0;
        border-radius: 0;
        box-shadow: none;
        @include mq_min_width($tablet-small) {
          display: flex;
          align-items: stretch;
          justify-content: space-between;
        }
        @include mq_min_width(881px) {
          margin-left: 30px;
          width: calc((100% - 1 * 30px) / 2);
          &:nth-child(2n + 1) {
            margin-left: 0;
          }
        }
        @include mq_min_width(1141px) {
          width: calc((100% - 2 * 30px) / 3);
          &:nth-child(2n + 1) {
            margin-left: 30px;
          }
          &:nth-child(3n + 1) {
            margin-left: 0;
          }
        }
        & > a {
          height: auto;
          border-radius: 0;
          position: relative;
          @include mq_min_width($tablet-small) {
            width: 29.80%;
          }
          img {
            border-radius: 0;
            width: 100%;
            min-height: 0;
            max-height: 329px;
            @include mq_min_width($tablet-small) {
              max-height: none;
              position: absolute;
              height: 100%;
              left: 50%;
              transform: translate(-50%, -50%);
              top: 50%;
              transition: transform 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;
            }
          }
        }
        &:hover,
        &:active,
        &:focus {
          & > a {
            img {
              @include mq_min_width($tablet-small) {
                transform: translate(-50%, -50%) scale(1.2);
              }
            }
          }
        }
        .gla-item-content {
          border-radius: 0;
          height: auto;
          padding: 35px 46px 40px 29px;
          display: block;
          bottom: auto;
          left: auto;
          right: auto;
          text-align: center;
          @include mq_min_width($tablet-small) {
            width: 70.2%;
            text-align: left;
          }
          & > a {
            display: block;
            text-decoration: none;
            text-align: left;
            &.lien2 {
              line-height: 26px;
              padding: 2px 0;
              font-weight: 600;
              position: relative;
              &:not(:nth-last-child(1)) {
                margin-bottom: 10px;
              }
              &:before {
                content: '';
                position: absolute;
                height: 0.76px;
                width: 34px;
                bottom: 0;
                left: 0;
                background: $theme-color-2;
              }
            }
            &.lien3 {
              @extend %button;
              margin-top: 30px;
            }
            &:nth-child(1):not(.lien2):not(.lien3) {
              & + .lien3 {
                margin-top: 0;
              }
            }
          }
        }
        .gla-item-desc,
        .gla-item-btn {
          display: none;
        }
        .gla-item-title {
          font-size: 25px;
          color: $textcolor-2;
          font-weight: 900;
          line-height: 40px;
          font-family: $font-2;
          margin-bottom: 20px;
        }
      }
    }
  }
}
