@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.sial-network {
  position: relative;
  display: flex;
  margin: 0 -25px 30px;
  padding: 40px;
  align-items: center;
  text-align: center;
  color: #fff;
  background: #00050A image('visual/sial-network-bg.png') center left / cover no-repeat;
  box-shadow: 0 2px 34px 0 rgba(#828282, .26);
  overflow: hidden;
  max-width: 610px;
  @include mq_min_width($tablet-small) {
    margin: 0 auto 30px;
  }
  @include mq_min_width($tablet-medium) {
    max-width: none;
    float: left;
    width: percentage(610px / $wrapper-size);
    min-height: 440px;
    margin: 0;
    background-size: contain;
    border-radius: 8px;
  }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    z-index: 0;
    background: #00050A;
    opacity: .5;
    @include mq_min_width($desktop-medium) {
      display: none;
    }
  }

  &__wrapper {
    z-index: 1;
    display: block;

    @include mq_min_width($desktop-medium) {
      margin-left: percentage(209px / 610px);
    }
  }

  &__title {
    display: block;
    margin-bottom: 3px;
    font-family: $font-2;
    font-size: 25px;
    font-weight: 900;
    line-height: 40px;
    text-align: center;
    color: #fff;
  }

  &__subtitle {
    display: block;
    margin-bottom: 28px;
    font-family: $font-1-alt;
    font-size: 20px;
    font-weight: 300;
    line-height: 31px;
    text-align: center;
    color: #fff;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin: 0 -40px;

    @include mq_min_width($desktop-medium) {
      margin: 0 -15px;
    }
  }

  &__item {
    display: block;
    margin: 0 15px 15px;
    font-family: $font-1;
    font-size: 16px;
    font-weight: 500;
    line-height: 21px;
  }

  &__city {
    position: relative;
    z-index: 0;
    display: inline-block;
    padding: 0 5px;
    font-family: $font-1;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    color: #fff;

    &::after { // background
      content: '';
      position: absolute;
      z-index: -1;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background: $theme-color-3;
      transform: skewX(-23.5deg);
    }

    &--alt {
      color: $theme-color-3;

      &::after {
        background: #fff;
      }
    }
  }

  &__link {
    @extend %button;
    margin-top: 20px;
  }

}
