@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.home-news {
  position: relative;
  padding-top: 50px;
  padding-bottom: 136px;
  @include clearfix;

  @include mq_min_width($tablet-medium) {
    padding-top: 70px;
    padding-bottom: 80px;
  }

  .gla-item-tag {
    display: none;
  }

  &::before { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    right: -25px;
    display: block;
    width: 100vw;
    background: $lightgrey-3;

    @include mq_min_width($desktop-medium) {
      right: 0;
    }
  }

  .inside {
    @include clearfix;

    @include mq_min_width($tablet-medium) {
      position: relative;
      max-width: (610px + 505px + 30px); // left + right columns + margin
    }
  }

  .main-title-with-link {
    margin: 0;
    border: 0;
  }

  .block-title {
    @extend .main-title;
    margin-bottom: 30px;
    text-align: center;

    @include mq_min_width($tablet-medium) {
      text-align: left;
    }
  }

  .link-view-all {
    @extend %button;
    position: absolute;
    left: 50%;
    bottom: 60px;
    margin: 0;
    white-space: nowrap;
    transform: translateX(-50%);
    @include mq_min_width($tablet-medium) {
      bottom: 0;
      left: calc(48.8% + 29px); // left columns + margins
      transform: none;
    }

    .icon {
      display: none;
    }
  }

  .grid-la-list {
    display: block;
    margin: 0;
  }

  .gla-item {
    width: 100%;
    max-width: 504px;
    height: 228px;
    margin: 0 auto 20px;

    @include mq_min_width($tablet-medium) {
      float: left;
      max-width: none;
      height: 166px;
      margin-right: 29px;
      width: calc(100% - 48.8% - 29px);
      &:not(:first-of-type) {
        margin-right: 0;
      }
    }

    > a {
      flex-direction: row;
    }

    &-title {
      margin-bottom: 5px;
      color: currentColor;
    }

    &-content {
      display: flex;
      flex-direction: column;
      width: 100%;
      min-height: 0;
      padding: percentage(20px / 505px) percentage(28px / 505px) percentage(21px / 505px);
      align-items: flex-start;
    }

    &-desc {
      display: none;
    }

    &-img,
    .at-illust,
    .gla-item-img,
    .la-item-img,
    .cx-lazy-load {
      width: 22%;
      min-width: 110px;
      border-radius: 0;

      @include mq_min_width($tablet-medium) {
        width: 123px;
        min-width: 0;
      }
    }

    &-author {
      position: relative;
      z-index: 0;
      order: -1;
      display: inline-block;
      margin-bottom: 15px;
      letter-spacing: -.4em;
      color: transparent;
    }

    .la-item-date {
      display: inline-block;
      padding: 0 10px;
      font-family: $font-1;
      font-size: 16px;
      font-weight: 500;
      font-style: normal;
      letter-spacing: 0;
      color: $textcolor-2;

      &:before {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        background-color: $theme-color-1;
        transform: skewX(-23.5deg);
      }
    }

    &:first-child {
      max-width: 610px;
      width: 100%;
      height: 437px;
      margin-bottom: 20px;

      @include mq_min_width($tablet-medium) {
        width: 48.8%;
        max-width: 610px;
        margin-bottom: 0;
      }

      .gla-item-title {
        font-family: $font-2;
        font-size: 24px;
        font-weight: 900;
        line-height: 28px;
        @include mq_min_width($tablet-medium) {
          max-height: 54.2px;
          overflow: hidden;
          flex-shrink: 0;
          flex-grow: 0;
        }
      }

      .gla-item-content {
        position: relative;
        z-index: 1;
        left: 0;
        top: 0;
        color: #fff;
        padding: percentage(176px / 610px) percentage(40px / 610px) percentage(46px / 610px);
        background: linear-gradient(180deg, rgba(#24221d, 0) 0%, rgba(#231f1b, .66) 100%);
      }

      .gla-item-desc {
        display: block;
        font-size: 18px;
        font-weight: 300;
        line-height: 28px;
      }

      .gla-item-img,
      .at-illust,
      .la-item-img,
      .cx-lazy-load {
        position: absolute;
        z-index: 0;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }

      .gla-item-author {
        margin-bottom: 22px;
      }
    }
  }
}
