@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.meet-buyers {
  position: relative;
  z-index: 1;
  padding-top: 50px;
  @if $is_mobile {
    padding-bottom: 80px;
  } @else {
    padding-bottom: 136px;
  }

  @include clearfix;

  @include mq_min_width($tablet-medium) {
    margin: 80px 0;
    padding-top: 70px;
    padding-bottom: 80px;
  }

  &::before { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    right: -25px;
    display: block;
    width: 100vw;
    background: $theme-color-1 image('visual/round-add-bg.svg') right bottom no-repeat;

    @include mq_min_width($tablet-medium) {
      right: 0;
    }
  }

  &__wrapper {
    border: 0;
  }

  &__title {
    margin-bottom: 39px;
    text-align: center;

    @include mq_min_width($tablet-medium) {
      font-size: 40px;
    }
  }

  &__list {
    position: relative;
    display: flex;

    @include mq_min_width($tablet-medium) {
      justify-content: center;
    }
  }

  &__item {
    width: 235px;
    padding: 0 10px;
    flex-shrink: 0;
    text-align: center;

    @include mq_min_width($tablet-medium) {
      width: 281px;
      padding: 0 30px;
    }

    &__icon {
      display: flex;
      width: 74px;
      height: 74px;
      margin: 0 auto 22px;
      font-size: 30px;
      line-height: 74px;
      align-items: center;
      justify-content: center;
      color: $theme-color-2;
      background: #fff;
      border-radius: 50%;

      &.icon-launch {
        font-size: 36px;
      }

      &.icon-contacts {
        font-size: 40px;
      }

      &.icon-truck {
        font-size: 29px;
      }

      &.icon-celebrity {
        font-size: 40px;
      }
    }

    &__text {
      font-size: 20px;
      font-weight: 300;
      line-height: 31px;
      color: $textcolor-2;
    }
  }
}
