@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.footer-bottom {
  clear: both;
  position: relative;
  z-index: 0;
  display: block;
  width: 100%;
  margin-top: 40px;
  background-color: $darkgrey-2;
  padding: 40px 0 30px;
  @include mq_min_width($tablet-medium) {
    padding: 20px 0;
  }
  @include mq_min_width($desktop-small) {
    margin-top: 50px;
  }

  &::before { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 50%;
    display: block;
    width: 100vw;
    height: 100%;
    background-color: inherit;
    transform: translateX(-50%);
  }

  &__menu {
    display: block;
    margin-bottom: 30px;

    @include mq_min_width($tablet-medium) {
      float: left;
      margin-bottom: 0;
    }

    &__item {
      position: relative;
      font-family: $font-1-alt;
      font-size: 14px;
      font-weight: 300;
      line-height: 33px;
      color: #fff;
      text-shadow: 0 2px 4px 0 rgba(#000, .5);

      @include mq_min_width($tablet-medium) {
        float: left;
        line-height: 28px;
      }

      & + & {
        @include mq_min_width($tablet-medium) {
          margin-left: 15px;
          padding-left: 18px;
        }

        &::before {
          @include mq_min_width($tablet-medium) {
            content: '-';
            position: absolute;
            left: 0;
          }
        }
      }
    }

    &__link {
      color: currentColor;
      text-decoration: none;
    }
  }

  &__logo {
    display: block;

    @include mq_min_width($tablet-medium) {
      float: right;
    }
  }
}
