@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.contacts-list {
  // Page /Infos-Pratiques/Equipe-commerciale
  body.node1309141 & {
    h1 {
      display: none;
    }

    h2 {
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      line-height: 1.6;
      color: $textcolor-2;
      border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    }

    .intro {
      padding: 0 10px 64px;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        z-index: -1;
        top: 0;
        left: 50%;
        display: block;
        height: 190%;
        width: 110vw;
        background-color: $lightgrey-3;
        transform: translate(-50%, -50%);
      }

      h3 {
        @extend .main-title;
      }

      p {
        @include mq_min_width($desktop-small) {
          padding: 0 25%;
        }

        color: $textcolor-2;
        font-size: 20px;
        font-weight: 300;
        line-height: 1.5;
        text-align: center;
      }
    }

    .cl-content {
      @extend %commercial-card-wrapper;
    }

    .cl-item {
      @extend %commercial-card;
      flex-direction: column;
      margin: 15px auto;
      max-width: 291px;

      @include mq_min_width(660px) {
        margin-left: 0;
        margin-right: 0;
      }

      @include mq_min_width($tablet-medium) {
        width: calc(50% - 30px);
        margin: 30px 0 0;
        max-width: calc(33% - 15px);
      }

      @include mq_min_width($desktop-small) {
        width: calc(50% - 30px);
        max-width: initial;
        margin: 90px 0 0;
        flex-direction: initial;

        &:nth-of-type(-n+2) {
          margin-top: 50px;
        }
      }

      &-illust {
        display: none;
      }

      &-photo {
        height: 212px;
        overflow: hidden;

        > img {
          width: 100%;
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;

          @include mq_min_width($desktop-small) {
            border-radius: 0;
            border-bottom-left-radius: 5px;
            border-top-left-radius: 5px;
          }
        }

        @include mq_min_width($desktop-small) {
          width: 212px;
        }
      }

      &-content {
        @extend %commercial-card-content;
        margin-top: 10px;
        padding: 20px 18px 26px 30px;
        min-height: 212px;

        @include mq_min_width($desktop-small) {
          width: calc(100% - 212px);
          padding-top: 30px;
          margin-left: auto;
          margin-top: 0;
          position: relative;
          max-height: 212px;
        }
      }

      &-title,
      &-subtitle {
        @extend %commercial-card-title;
      }

      &-title {
        @include text-ellipsis;
        order: 2;
        font-size: 18px;
        line-height: 1.55;
        margin-top: 11px;

        @include mq_min_width($desktop-small) {
          margin: 11px 0;
        }
      }

      &-subtitle {
        text-transform: capitalize;
        order: 3;
        margin-bottom: 11px;
        line-height: 1.6;

        @include mq_min_width($desktop-small) {
          width: 120%;
          position: absolute;
          top: 0;
          font-size: 25px;
          transform: translate3d(-240px, -60px, 0);
        }
      }

      &-phone,
      &-email {
        order: 4;
        color: $textcolor-2;
        margin-top: 5px;
        display: flex;
        width: 100%;

        @include mq_min_width($desktop-small) {
          align-items: center;
        }

        .label {
          display: none;
        }

        a {
          @include text-ellipsis;
          text-decoration: none;
        }
      }

      &-phone .icon-phone,
      &-email .icon-email {
        display: flex;
        margin-right: 11px;
        margin-top: 5px;

        @include mq_min_width($desktop-small) {
          margin-top: initial;
        }

        &::before {
          @include icomoon;
          font-size: 15px;
        }
      }

      &-phone {
        & + .cl-item-phone {
          margin-top: 0;

          .icon-phone {
            opacity: 0;
          }
        }

        .icon-phone {
          &::before {
            content: '\1f4de';
          }
        }
      }

      &-email {
        .icon-email {
          &::before {
            content: '\1f4e7';
          }
        }
      }

      &-social {
        @extend %commercial-card-social;
        order: 1;

        &.additional-informations {
          display: none;
        }
      }
    }

  }
}

body.node1309141 .quicklinks-cta--large {
  display: none;
}
