@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

body.node1318534 {

  #zone1 .edito {
    text-align: center;
    margin-bottom: 35px;

    h2 {
      @extend .main-title;
    }

    p {
      font-size: 20px;
      line-height: 1.5;

      em {
        font-size: 16px;
      }
    }
  }

  .contacts-list {
    margin: 70px -63px;
    padding: 51px 55px;
    position: relative;

    &::before {
      content: '';
      display: block;
      z-index: -1;
      background-color: $lightgrey-3;
      position: absolute;
      top: 0;
      left: 50%;
      width: 100vw;
      height: 100%;
      transform: translateX(-50%);

      @include mq_min_width($desktop-medium) {
        left: 0;
        transform: initial;
      }
    }

    .block-title {
      @extend .main-title;
      font-size: 40px;
    }

    .cl-content {
      @extend %commercial-card-wrapper;
    }

    .cl-item {
      @extend %commercial-card;
      flex-direction: column;
      max-width: 291px;
      margin: 15px 0;

      &-photo {
        height: 212px;
        overflow: hidden;

        > img {
          width: 100%;
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;
        }
      }

      &-content {
        padding: 25px 30px 15px;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        min-height: 138px;
        background-color: #fff;
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
      }

      &-title {
        @extend %commercial-card-title;
        order: 2;
        margin-bottom: 5px;
      }

      &-subtitle {
        order: 3;
        font-family: $font-1;
        font-weight: 300;
        text-transform: lowercase;

        &:first-letter {
          text-transform: capitalize;
        }
      }

      &-email,
      &-phone {
        display: none;
      }

      &-social {
        @extend %commercial-card-social;
        margin-bottom: 11px;
      }

      &-illust {
        @extend %displayNone;
      }
    }
  }

  .quicklinks-cta--large {
    .ql-list {
      flex-direction: row-reverse;
      justify-content: center;

      @include mq_min_width($tablet-medium) {
        flex-wrap: nowrap;
      }
    }

    .ql-item {
      width: 100%;

      .btn-primary {
        flex-flow: row nowrap;
        
        .icon {
          margin-bottom: 0;
          margin-right: 16px;
        }
      }

      @include mq_min_width(640px) {
        width: calc(49% - 30px);
        margin: 0 15px;
        max-width: 343px;
      }

      @include mq_min_width($tablet-medium) {
        width: 33.3334%;
      }
    }

    .icon-ampoule,
    .icon-trophee {
      &::before {
        @include icomoon;
      }
    }

    .icon-ampoule::before {
      content: '\e90d';
    }
    .icon-trophee::before {
      content: '\e90d';
    }
  }

  .detail-secteur {
    padding: 0;
    margin: 0 auto 25px;

    @include mq_min_width($desktop-small) {
      padding-left: 8.3334%;
      padding-right: 8.3334%;
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
    }

    &__additionnal {
      @include mq_min_width($desktop-small) {
        float: none;
        margin: 0 0 0 60px;
        width: 41.6667%;
        max-width: 400px;
        max-height: 400px;

        img {
          height: 400px;
          width: 100%;
        }
      }
    }

    &__main {
      @include mq_min_width($desktop-small) {
        margin: auto 0;
        width: 50%;
      }
    }

    &__list {
      margin-top: 0;
    }

    &__datas {
      background: none;
      margin: 0 0 0 auto;
      width: 100%;
      list-style: none;

      @include mq_min_width($desktop-small) {
        padding: 2.2rem 0 2.4rem;
      }

      > li {
        background-color: $lightgrey-3;
        margin-bottom: 10px;
        padding: 24px 20px 26px;
        text-align: center;

        &::before {
          margin: auto;
          justify-content: center;

          @include mq_min_width($tablet-medium) {
            margin: 0 2.5rem 0 0;
          }
        }

        &:not(:last-of-type):after {
          content: none;
        }

        p {
          align-items: center;

          @include mq_min_width($tablet-medium) {
            align-items: flex-start;
          }
        }

        @include mq_min_width($tablet-medium) {
          text-align: left;
        }

        @include mq_min_width($desktop-small) {
          padding-right: 62px;
          padding-left: 30px;
        }
      }
    }

    &:first-child {
      .custom-icon-coins,
      .custom-icon-increase,
      .custom-icon-flower {
        &::before {
          @include icomoon;
        }
      }

      .custom-icon-coins::before {
        content: '\1f4be';
      }
      .custom-icon-increase::before {
        content: '\1f961';
      }
      .custom-icon-flower::before {
        content: '\e90d';
      }
    }
  }

  .participate {
    padding: 50px 50% 47px 16.6667%;
    min-height: 250px;
    position: relative;
    margin-bottom: 60px;

    &::before {
      content: '';
      position: absolute;
      width: 100vw;
      height: 100%;
      top: 0;
      background: $theme-color-1 image('visual/sial-submit-product-bg.svg') bottom right 20% no-repeat;
      z-index: -1;
      left: 50%;
      transform: translateX(-50%);

      @include mq_min_width($desktop-small) {
        transform: initial;
        left: initial;
        right: 0;
      }
    }

    .btn-primary {
      @extend %button;
    }
  }


  .selection-stages {
    .detail-secteur {
      &__topText {
        position: absolute;
        top: 0;
        right: 0;
        background-color: rgba($textcolor-2, 0.7);
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        width: 100%;
        height: 100%;
        padding: 22px;

        p {        
          font-family: $font-2;
          font-size: 25px;
          font-weight: 300;
          line-height: 2;
          text-align: center;
        }

        strong {
          font-size: 82px;
          line-height: 0.5;

          &::after {
            content: '\A'; 
            white-space: pre-line;
          }
        }
      }

      &__additionnal {
        position: relative;
      }
    }
  }
}
