@import '../includes/variables';
@import '../includes/mixins';

.main-navigation {
  display: none;
  clear: right;
  float: left;
  padding: 0 0 0 18px;
  box-shadow: none;
  background: #fff;

  @include mq_min_width($tablet-medium) {
    display: block;
    padding: 0 0 0 5px;
  }

  @include mq_min_width($desktop-small) {
    padding: 0 0 0 18px;
  }

  .is-stuck & {
    clear: right;
  }
  @if $is_mobile {
    display: none !important;
  }
}

.mn-menu-line {
  border: 0;
}

.mn-menu-submenu {
  color: $textcolor-3;
  background: #fff;
  //outline: 1px solid rgba(black, .25);
}

.mn-link {
  .mn-item-lvl-1 > & {
    position: relative;
    padding: 19px 11px;
    color: $textcolor-3;
    font-family: $font-1;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: -0.1px;
    line-height: 26px;
    @include mq_min_width($desktop-medium) {
      padding: 19px 12px;
    }

    .is-stuck & {
      padding: 19px 11px;
      @include mq_min_width($desktop-medium) {
        padding: 19px 12px;
      }
    }

    &::after {
      position: absolute;
      content: '';
      left: 12px;
      right: 100%;
      bottom: 19px;
      border-bottom: 1px solid $theme-color-2;
      @include transition(.3s, right);
    }
  }

  .mn-menu-submenu & {
    padding: 7px 30px;

    &:last-child {
      padding-bottom: 15px;
    }
  }

  &-text {
    &::before {
      display: none;
    }
  }

  &.mn-link:hover,
  &.mn-link:focus,
  &.mn-link.is-active,
  .is-open &.mn-link {
    background: none;
    color: $textcolor-2;

    &::after {
      right: 12px;
    }

    .mn-menu-submenu & {
      padding-left: 30px;
      color: $theme-color-2;
    }
  }
}

.mn-item-lvl-1:first-child {
  .is-stuck & {
    display: block;
  }
}
