@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.block.edito-custom {
  margin: 30px auto 0;
  outline: none;
  @include clearfix;

  &:not(:first-child) {
    @include mq_min_width($tablet-medium) {
      margin-top: 100px;
    }
  }

  .inside {
    > * {
      margin-left: percentage(503px / 1100px) + percentage(60px / 1100px);
    }

    > .at-content:first-child {
      margin-left: 0;
    }
  }

  .at-illust,
  .gla-item-img,
  .la-item-img,
  .cx-lazy-load {
    @extend .at-illust;
    margin-left: 0;
  }

  h2 {
    @extend .at-main-title;
  }

  .at {
    &-main-title {
      @extend .main-title;
      display: block;
      margin-bottom: 20px;
    }

    &-content {
      display: block;
    }

    &-tag,
    &-publication {
      display: none;
    }

    &-illust {
      display: none;

      body.sial-innovation & {
        float: none;
        margin: 0 0 30px;
        object-fit: cover;
        font-family: 'object-fit: cover;';
        width: 100%;
      }

      @include mq_min_width($tablet-medium) {
        display: block;
        float: left;
        width: percentage(503px / 1100px);
        margin-top: -30px;
        margin-right: percentage(60px / 1100px);
      }
      @include mq_min_width($desktop-small) {
        body.sial-innovation & {
          width: 48.8%;
          order: 2;
          margin-bottom: 0;
        }
      }
    }
  }
}
