@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.catalog-order {
  margin-top: 40px !important;
  margin-bottom: 40px !important;

  @include mq_min_width($tablet-medium) {
    margin-top: 80px !important;
    margin-bottom: 80px !important;
  }

  .inside {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;

    @include mq_min_width($tablet-medium) {
      display: block;
    }

    > *:not(img) {
      margin-left: 0;
      @include mq_min_width($tablet-medium) {
        margin-left: percentage((503px + 107px) / $wrapper-size);
      }
    }
  }

  .cx-lazy-load,
  .gla-item-img,
  .la-item-img,
  .at-illust {
    margin-top: 40px;
    order: 99;

    @include mq_min_width($tablet-medium) {
      float: left;
      width: percentage(503px / $wrapper-size);
      margin-top: 0;
      margin-right: percentage(107px / $wrapper-size);
      order: 0;
    }
  }

  .block-title {
    @extend .main-title;
    font-size: 30px;
    line-height: 45px;
    margin-bottom: 10px;

    @include mq_min_width($tablet-medium) {
      font-size: 40px;
      line-height: 60px;
    }

    + p {
      margin-top: 10px;
      font-size: 18px;
      line-height: 28px;

      @include mq_min_width($tablet-medium) {
        margin-top: 20px;
        margin-bottom: 20px;
        font-size: 20px;
        line-height: 30px;
      }
    }
  }

  p {
    margin-top: 0;
    font-size: 16px;
    line-height: 31px;
  }

  strong {
    font-family: $font-1;
    font-weight: 700;
  }
}
