@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.insta-slider {
  margin-top: 70px;
  margin-bottom: 40px;

  @include mq_min_width($tablet-medium) {
    margin-bottom: 90px;
  }

  &__wrapper {
    position: relative;
    display: block;
    margin: 0 -15px;
    padding-bottom: 50px;

    @include mq_min_width($tablet-medium) {
      padding-bottom: 79px;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      display: block;
      height: 1px;
      width: 100vw;
      background: $lightgrey-1;
      transform: translateX(-50%);
      transition: opacity 1.2s $transition-type;
      opacity: 0;
      .is-ready.is-in-viewport & {
        opacity: 1;
      }
    }
  }

  &__list {
    position: relative;
    display: flex;
    width: 100%;
    margin: 0;
    align-items: center;
    justify-content: flex-start;
    transform-style: preserve-3d;
  }

  &__item {
    display: flex;
    width: 25%;
    margin: 0;
    padding: 0 5px;
    flex-shrink: 0;
    flex-grow: 0;
    transition: opacity 1.2s $transition-type;
    opacity: 0;
    @include mq_min_width($tablet-medium) {
      padding: 0 15px;
    }
    .is-ready.is-in-viewport & {
      opacity: 1;
    }
  }

  &__image {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    min-width: 100%;
    min-height: 100%;
    max-height: 100%;
    max-width: none;
    transform: translate(-50%, -50%);
    pointer-events: none;
    @include transition(7s, transform);

    .insta-slider__item:hover & {
      transform: translate(-50%, -50%) scale(1.2);
    }

    &__wrapper {
      position: relative;
      width: 100%;
      height: 0;
      padding-bottom: percentage(300px / 300px);
      border-radius: 6px;
      box-shadow: 0 2px 11px 0 rgba(#000, .16);
      overflow: hidden;

      .insta-slider__item:nth-child(odd) & {
        padding-bottom: percentage(367px / 300px);
      }
    }
  }
}
