@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

input[type=checkbox] {
  box-sizing: border-box;
  height: 18px;
  width: 18px;
  margin-right: 13px;
  border: 1px solid $textcolor-3;
  border-radius: 2px;
  background-color: transparent;
  appearance: none;
  vertical-align: middle;
  cursor: pointer;
  outline: none;

  &:checked {
    background: image('icons/check.svg') center center no-repeat;
    background-size: 75%;
  }
}
