@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.app-optimize-visit {
  position: relative;
  margin-top: 58px;
  padding-top: 50px;
  @include clearfix;

  @include mq_min_width($tablet-medium) {
    margin-top: 80px;
    padding-top: 70px;
    padding-bottom: 70px;
  }

  &::before { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    right: -25px;
    display: block;
    width: 100vw;
    background: $lightgrey-3;

    @include mq_min_width($tablet-medium) {
      right: 0;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    @include mq_min_width($tablet-medium) {
      flex-direction: row;
    }
  }

  &__title {
    @extend .main-title;
    margin-bottom: 12px;

    @include mq_min_width($tablet-medium) {
      padding-top: 30px;
      margin-bottom: 20px;
    }
  }

  &__content {
    font-size: 16px;
    line-height: 25px;

    p, ul {
      line-height: inherit;
      max-width: 513px;
    }

    ul li {
      margin-bottom: 10px;
    }
  }

  &__video {
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    display: block;
    min-width: 100%;
    min-height: 100%;
    transform: translate(-50%, -50%);
    @include transition(7s, transform);

    @if $is_mobile {
      position: relative;
      top: auto;
      left: auto;
      transform: none;
      min-width: 0;
      min-height: 0;
      max-height: 500px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
    } @else {
      .app-optimize-visit__video__container:hover & {
        transform: translate(-50%, -50%) scale(1.2);
      }
    }



    &__container {
      position: relative;
      z-index: 0;
      float: right;
      display: block;
      padding: 0 0 percentage(501px / 673px);
      margin: 40px -25px 0;
      max-height: 0;
      flex-shrink: 0;
      background-color: $textcolor-2;
      overflow: hidden;

      @if $is_mobile {
        float: none;
        padding: 0;
        max-height: none;
        margin-bottom: 40px;
        width: 100%;
        max-width: 810px;
        margin-left: auto;
        margin-right: auto;
      }

      @include mq_min_width($tablet-medium) {
        width: percentage(673px / $wrapper-size);
        margin: 0 (percentage(62px / $wrapper-size) * -1) 0 percentage(62px / $wrapper-size);
        padding-bottom: percentage(501px / $wrapper-size);
      }

      &::before { // gradient mask
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        background-color: rgba($textcolor-2, .4);
      }
    }

    &__icon {
      position: absolute;
      top: 50%;
      left: 50%;
      display: flex;
      width: 80px;
      height: 80px;
      line-height: 80px;
      font-size: 30px;
      text-align: center;
      align-items: center;
      justify-content: center;
      color: #fff;
      background: $theme-color-2;
      border-radius: 50%;
      text-decoration: none;
      transform: translate(-50%, -50%);

      &::before {
        margin-left: 4px;
      }
    }
  }
}
