@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.newsletter-form {

  @include mq_min_width($tablet-medium) {
    float: right;
    width: percentage(610px / $wrapper-size);
    margin-top: 0 !important;
  }


  .optin-container {
    float: none;
    width: auto;
    max-width: 388px;
    margin: 19px auto 0;
    text-align: left;
  }
}

.nf {
  &-title {
    padding: 0;
    font-family: $font-2;
    font-size: 30px;
    font-weight: 900;
    text-align: center;
    color: $textcolor-2;
    border: 0;
  }

  &-subtitle {
    font-size: 20px;
    font-weight: 300;
    line-height: 31px;

    > p {
      font-size: inherit;
      font-weight: inherit;
      line-height: inherit;
    }
  }

  &-main-content {
    display: flex;
    padding: 40px;
    align-items: center;
    text-align: center;
    color: $textcolor-2;
    background: $theme-color-1 image('visual/newsletter-bg.svg') bottom right no-repeat;
    box-shadow: 0 2px 34px 0 rgba(#828282, .26);
    max-width: 610px;
    margin: 0 -25px;
    @include mq_min_width($tablet-small) {
      margin: 0 auto;
    }
    @include mq_min_width($tablet-medium) {
      min-height: 440px;
      max-width: none;
      margin: 0;
      border-radius: 8px;
    }
  }

  &-form {
    &-item {
      label {
        display: none;
      }
    }

    &-input {
      float: none;
      width: auto;
      max-width: 388px;
      margin: 40px auto 0;
    }

    &-txt {
      width: 100%;
      height: 58px;
      border-radius: 7px;
      border: 0;
    }

    &-optin {
      text-align: left;
    }

    &-submit {
      margin-top: 42px;

      [class^='btn'] {
        @extend %button;
      }

      .icon {
        display: none;
      }
    }
  }
}
