@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.video-list {
  position: relative;
  z-index: 0;
  margin-top: 50px;
  margin-bottom: 50px;
  padding-bottom: 86px;

  @include mq_min_width($tablet-medium) {
    margin-top: 80px;
    margin-bottom: 80px;
    padding-bottom: 80px;
  }

  &::before { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: -50px;
    bottom: -50px;
    left: 50%;
    display: none;
    width: 100vw;
    background: $lightgrey-3;
    transform: translateX(-50%);

    @include mq_min_width($tablet-medium) {
      top: -360px;
      bottom: 0;
    }

    .meet-buyers + & {
      display: block;
    }
  }

  &__wrapper {
    display: block;
  }

  &__title {
    margin-bottom: 27px;
    text-align: center;

    @include mq_min_width($tablet-medium) {
      margin-right: 180px;
      margin-bottom: 60px;
      text-align: left;
    }
  }

  &__link {
    @extend %button;
    position: absolute;
    bottom: 0;
    right: 50%;
    transform: translateX(50%);
    white-space: nowrap;

    @include mq_min_width($tablet-medium) {
      top: 0;
      right: 0;
      bottom: auto;
      margin-top: 7px;
      transform: none;
    }
  }

  &__list {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;

    &__wrapper {
      display: block;
      @if $is_mobile {
        display: flex;
        align-items: flex-start;
        justify-content: center;
      }
    }
  }

  &__item {
    position: relative;
    z-index: 0;
    display: block;
    width: 258px;
    padding: 101px 26px 22px;
    flex-shrink: 0;
    text-decoration: none;
    border-radius: 6px;
    box-shadow: 0 4px 9px 0 rgba(#000, .13);
    overflow: hidden;

    @include mq_min_width($tablet-medium) {
      width: 396px;
      padding: 164px 40px 55px;
    }

    & + & {
      margin-left: 10px;

      @include mq_min_width($tablet-medium) {
        margin-left: 30px;
      }
    }

    &::after { // gradient mask
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: linear-gradient(180deg, rgba(#24221d, 0) 0%, rgba(#231f1b, .66) 100%);
    }

    &__title {
      font-family: $font-2;
      font-size: 20px;
      font-weight: 900;
      line-height: 30px;
      color: #fff;

      @include mq_min_width($tablet-medium) {
        font-size: 25px;
        line-height: 35px;
      }
    }

    &__icon {
      position: absolute;
      top: 51px;
      display: flex;
      width: 32px;
      height: 32px;
      font-size: 12px;
      line-height: 32px;
      text-align: center;
      align-items: center;
      justify-content: center;
      color: #fff;
      background-color: $theme-color-2;
      border-radius: 50%;

      @include mq_min_width($tablet-medium) {
        top: 80px;
        width: 50px;
        height: 50px;
        font-size: 19px;
        line-height: 50px;
      }

      &::before {
        margin-left: 3px;
      }
    }

    &__img {
      position: absolute;
      z-index: -2;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      overflow: hidden;

      img {
        position: absolute;
        left: 50%;
        top: 50%;
        display: block;
        max-width: none;
        min-width: 100%;
        min-height: 100%;
        transform: translate(-50%, -50%);
        @include transition(7s, transform);

        .video-list__item:hover & {
          transform: translate(-50%, -50%) scale(1.2);
        }
      }
    }
  }
}
