.custom_generic_progressBar {
  position: absolute;
  bottom: 38px;
  display: block;
  width: 100%;
  height: 2px;
  background: #fff;

  &__btn,
  > span {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform-origin: left center;
    background: $theme-color-2;
  }

  .ql-item & {
    bottom: 134px;
  }

  &.swiper-pagination-lock {
    display: none;
  }

  .tiles-list &,
  .video-list & {
    position: relative;
    bottom: auto;
    margin-top: 30px;
  }
}
