@import '../includes/variables';
@import '../includes/mixins';

.lang-switcher {
  position: relative;
  z-index: 1;
  float: right;
  margin: 17.6px 0;
  font-size: 14px;
  background: none;
  border-left: 1px solid rgba($lightgrey-1, .3);

  @include mq_min_width($desktop-small) {
    margin: 9px 0;
  }

  .is-stuck & {
    display: block;
  }

  abbr {
    text-decoration: none;
    border: 0;
  }

  @if $is_mobile {
    display: none !important;
  }
}

.ls {
  &-trigger {
    padding: 0 20px;
    font-family: $font-1-alt;
    font-size: 14px;
    font-weight: 300;
    line-height: 20px;
    color: $textcolor-1;

    &:focus {
      outline: 0;
    }

    &::after { // carret icon
      display: none;
    }
  }

  &-lang-list {
    line-height: inherit;
  }

  &-lang-link {
    padding: 0.3em;
    text-align: center;

    &:hover,
    &:focus {
      color: #fff;
      background: $theme-color-2;
    }
  }

  &-lang-item {
    &.is-active {
      display: none;
    }
  }
}

