@if $is_mobile {
  .custom-menu-mobile-container {
    position: fixed;
    top: 62px;
    height: calc(100% - 62px);
    overflow: auto;
    width: 100%;
    left: 0;
    z-index: 19;
    padding: 15px 0;
    background: $lightgrey-3;
    transform: translateX(-101%);
    transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
    pointer-events: none;
    opacity: 0;
    overflow-x: hidden;
    max-width: 100vw;
    .custom-menu-mobile-open & {
      transform: translateX(0);
      pointer-events: auto;
      opacity: 1;
    }
    ul {
      padding: 0;
      margin: 0;
      list-style: none;
    }
    .custom-menu-mobile-inner-1 .mn-link,
    .custom-menu-mobile-inner-2 .btn-primary,
    .custom-menu-mobile-inner-3 a,
    .custom-menu-mobile-inner-4 a {
      display: inline-block;
      color: $textcolor-2;
      font-family: $font-1;
      font-size: 14px;
      font-weight: 600;
      letter-spacing: -0.1px;
      line-height: 26px;
      text-transform: uppercase;
      padding: 10px 15px;
      text-decoration: none;
      background: transparent;
      border: 0;
      width: 100%;
      text-align: left;
      transition: color 0.2s ease-in-out;
      &:hover,
      &:active {
        padding-left: 15px;
        color: $theme-color-2;
      }
    }
    .custom-menu-mobile-inner-4 {
      padding-left: 15px;
      padding-right: 15px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      li {
        &.is-active {
          order: 1;
          a {
            color: $theme-color-2;
            padding-left: 0;
          }
        }
        &:not(.is-active) {
          order: 2;
          a {
            padding-left: 0;
          }
        }
      }
    }

    .custom-menu-mobile-inner {
      & + .custom-menu-mobile-inner {
        position: relative;
        padding-top: 15px;
        margin-top: 15px;
        &:before {
          content: '';
          position: absolute;
          background: rgba($lightgrey-1, 0.6);
          height: 1px;
          width: calc(100% - 30px);
          top: 0;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
    .custom-menu-mobile-inner-1 {
      .mn-menu-item {
        &,
        &.mn-item-lvl-2,
        &.mn-item-lvl-1 {
          display: block;
          padding: 0;
          background: transparent;
          color: inherit;
          border: 0;
          min-height: 0;
        }
        .mn-link {
          background: transparent;
          &:after {
            content: none;
          }
        }
        &.mn-item-prev {
          padding-bottom: 10px;
          margin-bottom: 15px;
          position: relative;
          &:after {
            content: '';
            position: absolute;
            background: rgba($lightgrey-1, 0.6);
            height: 1px;
            width: calc(100% - 30px);
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
          }
          & > .mn-link {
            position: relative;
            padding-left: 50px;
            color: $theme-color-2;
            &:after {
              @include icomoon;
              content: '\1f51d';
              font-size: 9px;
              position: absolute;
              top: 50%;
              left: 15px;
              transform: translateY(-50%) rotate(-90deg);
              display: flex;
              align-items: center;
              justify-content: center;
              width: 25px;
              height: 25px;
              right: auto;
            }
          }
        }
        &.mn-item-has-submenu {
          position: static;
          &:after {
            content: none;
          }
          & > .mn-link {
            position: relative;
            padding-right: 50px;
            &:before {
              @include icomoon;
              content: '\1f51d';
              font-size: 9px;
              position: absolute;
              top: 50%;
              right: 15px;
              transform: translateY(-50%) rotate(90deg);
              display: flex;
              align-items: center;
              justify-content: center;
              width: 25px;
              height: 25px;
            }
            &.visible {
              & + .mn-menu-submenu {
                opacity: 1;
                pointer-events: auto;
                transform: translateX(0);
              }
            }
          }
        }
        .mn-menu-submenu {
          min-width: 0;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          display: block;
          z-index: 1;
          background: $lightgrey-3;
          height: 100%;
          overflow: auto;
          padding: 15px 0;
          transform: translateX(101%);
          pointer-events: none;
          opacity: 0;
          transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
        }
      }
    }
  }
}
