@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.why-visit {
  margin: 30px auto 0;
  overflow: hidden;
  @if not $is_mobile {
    max-width: 750px;
  }
  width: 100%;
  @include mq_min_width($tablet-medium) {
    margin: 40px 0 0;
  }
  @include mq_min_width($desktop-medium) {
    margin: 100px auto 0;
    max-width: 1100px;
    width: auto;
  }
  &__title {
    margin-bottom: 10px;
    font-family: $font-2;
    font-size: 20px;
    font-weight: 900;
    line-height: 40px;
    color: $textcolor-2;

    @include mq_min_width($tablet-medium) {
      margin-bottom: 35px;
      font-size: 25px;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    position: relative;
    display: block;
    width: 100%;
    margin-bottom: 25px;
    padding-left: 70px;
    flex-shrink: 0;
    text-decoration: none;

    @include mq_min_width($desktop-small) {
      width: 50%;
    }

    &__icon {
      position: absolute;
      top: 0;
      left: 0;
      float: left;
      width: 70px;
      height: 50px;
      font-size: 37px;
      color: $lightgrey-1;
    }

    &__title {
      font-family: $font-1;
      font-size: 16px;
      font-weight: 500;
      line-height: 26px;
      color: $lightgrey-1;
    }

    &__link {
      @extend %button-underline;

      &::after {
        .why-visit__item:hover & {
          width: 100%;
        }
      }
    }
  }
}
