@import '../../includes/variables';
@import '../../includes/mixins';

.site-logo {
  float: left;
  padding: 13px 25px;
  font-family: $font-2;
  color: $textcolor-2;
  background: #fff;

  @if $is_mobile {
    margin-right: auto;
    padding: 0;
  }

  @include mq_min_width($tablet-medium) {
    min-width: (242.5px + 105px + 40px);
    padding: 15px 15px 0;
  }

  @include mq_min_width($desktop-small) {
    padding: 26px 30px;
    border-right: 1px solid rgba($lightgrey-1, .3);
  }

  &__img {
    float: left;
    display: block;
    width: 59px;
    height: 26px;
    margin-top: 5px;
    margin-right: 9px;
    background: image('logo-sial.svg') center center / contain no-repeat;

    @if $is_mobile {
      margin-top: 3px;
    }

    @include mq_min_width($tablet-medium) {
      width: 91px;
      height: 40px;
      margin-top: 0;
      margin-right: 18px;
    }

    @include mq_min_width($desktop-small) {
      width: 105px;
      height: 46px;
    }

    > * {
      @include visually-hidden;
    }
  }

  &__date {
    font-size: 12px;
    font-weight: 900;
    letter-spacing: -0.13px;
    white-space: nowrap;

    @if $is_mobile {
      float: right;
    }

    @include mq_min_width($tablet-medium) {
      font-size: 15px;
      letter-spacing: -0.2px;
    }

    @include mq_min_width($desktop-small) {
      font-size: 19px;
    }
  }

  &__location {
    font-size: 9px;
    font-weight: 300;
    letter-spacing: 0.76px;
    white-space: nowrap;
    text-transform: uppercase;

    @if $is_mobile {
      float: right;
      clear: right;
    }

    @include mq_min_width($tablet-medium) {
      font-size: 12px;
      letter-spacing: 1.18px;
      margin-top: -3px;
    }

    @include mq_min_width($desktop-small) {
      font-size: 14px;
      margin-top: 0;
    }
  }
}
