@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.grid-la-list {
  .home-events & {
    margin-left: 0;
    margin-bottom: 0;

    @if $is_mobile {
      justify-content: center;
      .gla-item {
        @include mq_min_width($tablet-small) {
          margin-left: 15px;
          margin-right: 15px;
        }
      }
    }

    @include mq_min_width($desktop-small) {
      margin-left: -30px;
    }
  }
}

.gla {
  &-item {
    padding: 0;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 4px 9px 0 rgba(#000, .13);

    .home-events & {
      position: relative;
      height: 440px;
      width: 270px;
      margin: 65px auto 39px;
      overflow: visible;

      @include mq_min_width($desktop-small) {
        width: calc(100% * 1 / 3 - 30px - .01px);
        margin-left: 30px;
        margin-bottom: 0;
      }
    }

    > a {
      display: flex;
      height: 100%;
      flex-direction: column;
      text-decoration: none;
      border-radius: 10px;
      overflow: hidden;

      &:hover,
      &:focus,
      &:active {
        color: currentColor;
        .at-illust,
        .gla-item-img,
        .la-item-img,
        .cx-lazy-load { // TODO: remove when multiple class issue resolved
          transform: scale(1.2);
          @include transition(7s, transform);
        }
      }
    }

    &-content {
      position: static;
      z-index: 1;
      display: flex;
      height: 100%;
      min-height: 200px;
      padding: 20px;
      flex-direction: column;
      text-align: left;
      background-color: #fff;
      @include border-bottom-radius(6px);

      .gla-item > a:hover &,
      .gla-item > a:focus & {
        color: currentColor;
        background-color: #fff;
      }
    }

    &-img,
    .at-illust,
    .gla-item-img,
    .la-item-img,
    .cx-lazy-load {
      z-index: 0;
      min-height: 160px;
      flex-shrink: 0;
      @include border-top-radius(6px);
      object-fit: cover;
      font-family: 'object-fit: cover;';
      @include transition(1s, transform);

      .home-events & {
        height: 200px;
      }

      .gla-item > a:hover &,
      .gla-item > a:focus & {
        transform: scale(1.2);
        @include transition(7s, transform);
      }
    }

    &-title {
      margin: 0;
      font-size: 18px;
      line-height: 26px;
      color: $textcolor-1;

      .home-events & {
        position: absolute;
        top: -65px;
        left: 0;
        width: 100%;
        padding-bottom: 5px;
        font-family: $font-2;
        font-size: 15px;
        font-weight: 500;
        letter-spacing: 1.3px;
        line-height: 37px;
        color: $textcolor-4;
        border-bottom: 1px solid rgba(#000, .15);
      }

      .gla-item > a:hover &,
      .gla-item > a:focus & {
        color: currentColor;
      }

      &::after {
        .home-events & {
          @include icomoon;
          content: map_get($icon-list, 'plus');
          float: right;
          font-size: 16px;
          line-height: 37px;
          font-weight: 400;
          vertical-align: middle;
          color: $theme-color-2;
          @include transition(.3s, transform);
        }

        .gla-item > a:hover &,
        .gla-item > a:focus & {
          transform: scale(1.2);
        }
      }
    }

    &-desc {
      .home-events & {
        margin: 0;
        font-family: $font-2;
        font-size: 18px;
        font-weight: 900;
        line-height: 28px;
        color: $textcolor-4;
      }
    }

    &-btn {
      order: 99;

      .gla-item-content & {
        margin-top: auto;
      }
    }
  }

  &-btn {
    &-read-more {
      @extend %button-underline;

      &:focus,
      &:hover,
      &:active {
        color: currentColor;
        background: none;
        border: 0;
      }

      .gla-item > a:hover &,
      .gla-item > a:focus & {
        &::after {
          width: 100%;
        }
      }
    }
  }
}

