@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.contacts-list {
  $page-gouvernance: '.node1315331'; // Infos-Pratiques/Direction-SIAL-Paris

  #{$page-gouvernance} & {
    position: relative;
    z-index: 0;
    margin-bottom: 70px;
  }

  .block + & {
    #{$page-gouvernance} & {
      margin-top: 80px;
    }
  }

  &::before { // overflow background
    #{$page-gouvernance} & {
      content: '';
      position: absolute;
      z-index: -1;
      top: 0;
      bottom: 0;
      left: -25px;
      display: block;
      width: 100vw;
      background-color: $lightgrey-3;
    }
  }

  .inside {
    #{$page-gouvernance} & {
      display: flex;
      max-width: 930px;
      margin: 0 auto;
      padding-top: 50px;
      padding-bottom: 70px;
      flex-direction: column;
    }
  }

  h1 {
    #{$page-gouvernance} & {
      @extend .main-title;
      order: 0;
      margin-bottom: 17px;
      text-align: center;
    }
  }

  h2 {
    #{$page-gouvernance} & {
      order: 10;
      margin-bottom: 50px;
      padding: 0;
      font-family: $font-1-alt;
      font-size: 20px;
      font-weight: 300;
      line-height: 25px;
      text-align: center;
      color: currentColor;
      border: 0;
    }
  }

  .intro {
    #{$page-gouvernance} & {
      order: 90;
    }

    h6 {
      margin-bottom: 12px;
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      line-height: 40px;
      color: $textcolor-2;
    }

    p {
      #{$page-gouvernance} & {
        margin-bottom: 30px;
        font-size: 16px;
        font-weight: 500;
        line-height: 25px;
      }

      &:first-child {
        #{$page-gouvernance} & {
          margin-top: 0;
        }
      }

      &:last-child {
        #{$page-gouvernance} & {
          margin-bottom: 0;
        }
      }
    }

    hr {
      #{$page-gouvernance} & {
        margin: 30px 0;
        border: 0;
        border-bottom: 1px solid $lightgrey-1;
      }
    }

    a.externe {
      #{$page-gouvernance} & {
        @extend %button-underline;
        font-weight: 700;
        color: $theme-color-2;

        &::after {
          width: 0;
        }
      }
    }
  }

  .cl {
    &-content {
      #{$page-gouvernance} & {
        order: 20;
        display: flex;
        margin-bottom: 50px;
        flex-wrap: wrap;
        justify-content: center;
      }
    }

    &-item {
      #{$page-gouvernance} & {
        display: flex;
        flex-direction: column;
        width: 290px;
        margin: 0;
        padding: 0;
        background: #fff;
        border: 0;
        border-radius: 5px;
        box-shadow: 0 4px 21px 0 rgba(#000, .07);
        overflow: hidden;
      }

      + .cl-item {
        #{$page-gouvernance} & {
          margin-top: 20px;

          @include mq_min_width($tablet-medium) {
            margin-top: 0;
            margin-left: 30px;
          }
        }
      }

      > * {
        #{$page-gouvernance} & {
          display: block;
          padding: 0;
        }
      }

      &-photo {
        #{$page-gouvernance} & {
          order: -1;
          position: relative;
          height: 217px;
          text-align: center;
          overflow: hidden;
        }

        img {
          #{$page-gouvernance} & {
            position: absolute;
            top: 50%;
            left: 50%;
            min-width: 100%;
            transform: translate(-50%, -50%);
          }
        }
      }

      &-title {
        #{$page-gouvernance} & {
          font-family: $font-2;
          font-size: 18px;
          font-weight: 900;
          line-height: 28px;
          color: currentColor;
        }
      }

      &-subtitle {
        #{$page-gouvernance} & {
          font-size: 16px;
          font-weight: 300;
          text-transform: none;
        }
      }

      &-content {
        #{$page-gouvernance} & {
          display: flex;
          flex-direction: column;
          padding: 25px 30px;
          color: $textcolor-2;
        }
      }

      &-email,
      &-adress {
        #{$page-gouvernance} & {
          display: none;
        }
      }

      &-social {
        #{$page-gouvernance} & {
          order: -1;
          display: block;
          margin: 0;
          padding: 0;
          font-size: 27px;
        }

        > a {
          #{$page-gouvernance} & {
            color: $lightgrey-1;
            margin-right: 6px;
            @include transition(.2s, color);
          }

          &:focus,
          &:hover {
            opacity: 1;
            color: $theme-color-2;
          }
        }
      }
    }
  }
}
