@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.app-easy-network {
  position: relative;
  z-index: 0;

  &::before { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    right: -25px;
    height: 100%;
    width: 100vw;
    background: #EAEEF6;

    @include mq_min_width($tablet-medium) {
      right: 0;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
    padding-top: 40px;
    padding-bottom: 89px;
    background: #EAEEF6 image('visual/smartphone-bg.svg') bottom left no-repeat;

    @include mq_min_width($tablet-medium) {
      flex-direction: row;
      padding-right: 100px;
      padding-bottom: 39px;
    }
    @include mq_min_width($desktop-medium) {
      margin-left: -73px;
      padding-left: 73px;
    }
  }

  &__content {
    font-size: 18px;
    font-weight: 300;
    line-height: 28px;
    text-align: center;

    @include mq_min_width($tablet-medium) {
      font-size: 20px;
      text-align: left;
    }
  }

  &__link {
    @extend %button;
    flex-shrink: 0;
    min-width: 236px;

    & + & {
      margin-top: 14px;
    }

    &__container {
      display: flex;
      flex-direction: column;
      margin-top: 20px;
      align-items: center;
      justify-content: center;

      @include mq_min_width($tablet-medium) {
        margin-top: 0;
        margin-left: 112px;
      }
    }
  }

  &__store {
    height: 47px;

    & + & {
      margin-left: 8px;

      @include mq_min_width($tablet-medium) {
        margin-top: 14px;
        margin-left: 0;
      }
    }

    &__container {
      margin-top: 36px;
      display: flex;
      flex-shrink: 0;
      align-items: center;
      justify-content: center;

      @include mq_min_width($tablet-medium) {
        margin-top: 0;
        margin-left: 58px;
        flex-direction: column;
      }
    }
  }
}
