.testimonials {
  &__wrapper {
    display: block;
    max-width: 1037px;
    margin: 40px auto;
    @include mq_min_width($tablet-medium) {
      margin: 80px auto;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 30px;
    @include mq_min_width($tablet-medium) {
      margin-bottom: 50px;
    }
  }

  &__quote {
    position: relative;
    z-index: 0;
    width: 100%;
    padding: 40px 15px 30px;
    flex-shrink: 0;
    text-align: center;
    box-shadow: 0 4px 9px 0 rgba(#000, .13);
    overflow: hidden;

    &::before,
    &::after { // Quotes icon background
      @include icomoon;
      position: absolute;
      z-index: -1;
      display: block;
      color: $lightgrey-3;
    }

    &::before {
      content: '\e907';
      font-size: 60px;
      left: 20px;
      top: 20px;
      @include mq_min_width($tablet-small) {
        font-size: 80px;
      }
      @include mq_min_width($tablet-medium) {
        font-size: 100px;
        left: 60px;
        top: 40px;
      }
    }

    &::after {
      content: '\e908';
      right: 20px;
      font-size: 50px;
      top: auto;
      bottom: 20px;
      @include mq_min_width($tablet-small) {
        font-size: 60px;
      }
      @include mq_min_width($tablet-medium) {
        top: 140px;
        right: 90px;
        bottom: auto;
        font-size: 80px;
      }
    }

    &.is-active {
      z-index: 1;
    }

    @include mq_min_width($desktop-small) {
      &:not(.is-active) {
        position: absolute !important;
        visibility: hidden !important;
      }
    }

    &__list {
      display: flex;
      width: 100%;
      @include mq_min_width($desktop-small) {
        transform: none !important;
      }
    }

    &__wrapper {
      position: relative;
    }

    &__text {
      max-width: 685px;
      margin: 20px auto;
      font-size: 19px;
      font-weight: 500;
      line-height: 32px;
      text-align: center;
      color: $textcolor-2;
    }

    &__author {
      display: none;
    }

    &__link {
      @extend %button-underline;
    }
  }

  &__author {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    max-width: 73px;
    @include transition(max-width, 1s);

    & + & {
      margin-left: 0;
      @include mq_min_width($tablet-medium) {
        margin-left: 30px;
      }
      @include mq_min_width($desktop-small) {
        margin-left: 50px;
      }
    }

    &__indicator {
      position: absolute;
      left: 36%;
      top: -40px;
      width: 100px;
      display: block;
      height: 60px;
      pointer-events: none;
      overflow: hidden;
      &:before {
        content: '';
        position: absolute;
        top: -25px;
        display: block;
        width: 70px;
        height: 40px;
        background: #fff;
        box-shadow: 0 4px 9px 0 rgba(#000, .13);
        transform: translateX(-50%) rotate(-30deg);
        opacity: 0;
        left: 50%;
        @include transition(opacity);
      }
    }

    &.is-active {
      max-width: 100%;
      flex-shrink: 0;
      flex-direction: column;
      justify-content: flex-start;
      padding-top: 15px;
      @include mq_min_width($tablet-small) {
        max-width: 475px;
        flex-direction: row;
        justify-content: normal;
        padding-top: 0;
      }
      .testimonials__author__indicator {
        &:before {
          opacity: 1;
        }
      }
    }

    &:not(.is-active) {
      cursor: pointer;
      display: none;
      @include mq_min_width($tablet-medium) {
        display: flex;
      }
    }

    &__wrapper {
      position: relative;
      margin-top: 40px;
    }

    &__list {
      display: flex;
      justify-content: center;
      height: auto;
      @include mq_min_width($tablet-small) {
        height: 88px;
      }
    }

    &__content {
      width: 0;
      opacity: 0;
      overflow: hidden;
      margin-left: 0;
      @include transition($duration: 1s);
      transition-property: width, opacity;

      .testimonials__author.is-active & {
        width: auto;
        opacity: 1;
        margin-left: 0;
        margin-top: 15px;
        text-align: center;
        @include mq_min_width($tablet-small) {
          margin-left: 20px;
          margin-top: 0;
          text-align: left;
        }
      }
    }

    &__img {
      width: 73px;
      flex-shrink: 0;
      user-select: none;
      will-change: width;
      .testimonials__author.is-active & {
        width: 88px;
        transition: width 0.25s linear, height 0.25s linear;
      }
    }

    &__name {
      font-family: $font-2;
      font-size: 18px;
      font-weight: 900;
      line-height: 1;
      margin-bottom: 10px;
      @include mq_min_width($tablet-small) {
        white-space: nowrap;
      }
    }

    &__job {
      font-size: 13px;
      font-weight: 500;
      line-height: 1;
      color: $lightgrey-2;
      @include mq_min_width($tablet-small) {
        white-space: nowrap;
      }
    }
  }

  &__rating {
    display: flex;
    font-size: 12px;
    line-height: 1;
    align-items: center;
    justify-content: center;
    color: $theme-color-2;

    .icon + .icon {
      margin-left: 10px;
    }
  }
  &__arrows-container {
    @include slider-arrows;
    @include mq_min_width($tablet-medium) {
      display: none;
    }
  }
}
