@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.top-quicklinks {
  float: right;
  display: none;
  margin-top: 20px;

  @include mq_min_width(1331px) {
    display: flex;
  }

  > span { // block title
    float: left;
    margin-right: 7px;
    font-size: 14px;
    font-weight: 400;
    line-height: 26px;
  }

  .btn-primary {
    display: block;
    padding: 0 10px;
    font-family: $font-1;
    font-size: 14px;
    line-height: 24px;
    text-transform: none;
    color: currentColor;
    background: none;
    border: 1px solid $lightgrey-1;
    border-radius: 7px;
    @include transition(.3s, color);

    &:hover,
    &:focus {
      color: $theme-color-2;
    }

    .txt-btn {
      display: block;
    }
  }
}

.ql {
  &-list {
    .site-banner & {
      display: flex;
    }
  }

  &-item {
    & + & {
      .site-banner & {
        margin-left: 10px;
      }
    }
  }
}
