//============================================================
// Hide Input number controls
//============================================================

@mixin hide-input-number-controls {
  -moz-appearance: textfield;
  @at-root #{&}::-webkit-inner-spin-button {
    appearance: none;
    margin: 0;
  }
  @at-root #{&}::-webkit-outer-spin-button {
    appearance: none;
    margin: 0;
  }
}

