@import '../includes/variables';
@import '../includes/mixins';

.exhibitors-slider {
  position: relative;
  padding-bottom: 120px;
  margin-top: 40px;
  margin-bottom: 0;
  clear: both;

  @at-root body.page-rubric & {
    padding-top: 40px;
  }

  @include mq_min_width($tablet-medium) {
    margin-bottom: 20px;
    padding-top: 70px;
    padding-bottom: 80px;
    @at-root body.page-rubric & {
      padding-top: 70px;
    }
  }

  @include mq_min_width($desktop-small) {
    margin-top: 100px;
  }

  .front & {
    @include mq_min_width($tablet-medium) {
      padding-top: 0;
    }
  }

  /* Homepage T2 */
  .front .home-search + & {
    padding-bottom: 0;
    margin: 40px 0;
    @include mq_min_width($tablet-medium) {
      margin: 80px 0;
      padding-bottom: 0;
    }
    .exhibitors-slider__progressbar {
      position: relative;
      bottom: auto;
      margin-top: 30px;
    }
  }

  .catal-ex-header {
    display: none;
  }

  &::before { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    bottom: 0;
    right: -25px;
    display: block;
    width: 100vw;
    background-color: $lightgrey-3;

    .front & {
      display: none;
    }

    @include mq_min_width($tablet-medium) {
      right: 0;
    }
  }

  &__header {
    margin-bottom: 30px;
    overflow: hidden;

    @include mq_min_width($tablet-medium) {
      padding-bottom: 20px;
      border-bottom: 1px solid $lightgrey-4;
    }

    .main-title {
      margin-bottom: 8px;

      @include mq_min_width($tablet-medium) {
        margin-right: 390px;
      }
    }

    p {
      margin: 8px 0 0;
      color: $textcolor-2;
      font-size: 20px;
      font-weight: 300;
      line-height: 31px;
    }
  }


  &__btn {
    float: left;
    display: block;
    width: 49px;
    height: 49px;
    background: center center / contain no-repeat;
    border: 0;
    border-radius: 50%;
    @include transition(.3s, opacity);

    &.disabled {
      cursor: not-allowed;
      opacity: 0.4;
    }

    &:focus {
      outline: 0;
    }

    & + & {
      margin-left: 28px;
    }

    &--prev {
      background-image: image('icons/arrow-left--rounded.svg');
    }

    &--next {
      background-image: image('icons/arrow-right--rounded.svg');
    }

    &__container {
      display: none;

      @include mq_min_width($tablet-medium) {
        display: block;
        float: right;
        margin-top: -65px;
        margin-right: 43px;
      }

      .front & {
        @include mq_min_width($tablet-medium) {
          margin-right: 0;
        }
      }
    }
  }

  &__progressbar {
    position: absolute;
    bottom: 76px;
    display: block;
    width: 100%;
    height: 2px;
    background: $lightgrey-5;

    @include mq_min_width($tablet-medium) {
      display: none;
    }

    &__btn,
    > span {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      transform-origin: left center;
      background: $theme-color-2;
    }
  }

  .edito {
    display: none;
  }

  .catal-ex {
    clear: both;
    position: relative;
    display: flex;
    margin: 0 -15px;
  }

  .catal-ex-item {
    flex-direction: column;
    width: 262px;
    margin: 0 15px;
    padding: 0;
    flex-shrink: 0;
    background-color: #fff;
    border: 0;
    border-radius: 5px;
    box-shadow: 0 4px 9px 0 rgba(#000, .13);
    overflow: hidden;

    &-content {
      flex: 1 1 auto;
      padding: 20px;
    }

    &-group-title {
      min-height: auto;
      margin: 0;
      padding: 0;
      border: 0;
    }

    &-stand {
      display: none;
    }

    &-title {
      color: $textcolor-2;
      font-family: $font-2;
      font-size: 18px;
      font-weight: 900;
      line-height: 26px;

      a:hover, a:focus {
        color: currentColor;
        text-decoration: none;
      }
    }

    &-desc {
      display: none;
    }

    &-img {
      width: 100%;
      height: 130px;
      min-height: auto;
      margin: 0;
      background-color: $lightgrey-3;

      img {
        display: block;
        width: auto;
        margin: auto;
      }
    }

    &-buttons-small {
      display: none;
    }

    &-cat {
      &-title {
        display: none;
      }

      &-item {
        margin: 0;
        color: $textcolor-2;
        font-family: $font-1-alt;
        font-size: 16px;
        font-weight: 300;
        line-height: 26px;
      }

      &-link {
        color: currentColor;
        text-decoration: none;
      }

      &-txt {
        &::before {
          content: '#';
        }
      }

      &-icon {
        display: none;
      }
    }

    &-buttons {
      display: none;
    }
  }
}
