@mixin slider-arrows {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-top: 30px;
  .arrow {
    width: 49px;
    height: 49px;
    user-select: none;
    cursor: pointer;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $theme-color-2;
    color: $theme-color-2;
    margin: 0 14px;
    outline: 0;
    background-color: transparent;
    transition: opacity 0.2s ease-in-out, background-color 0.2s ease-in-out, color 0.2s ease-in-out;
    &.swiper-button-disabled {
      cursor: not-allowed;
      opacity: 0.4;
    }
    &:not(.swiper-button-disabled) {
      &:hover,
      &:active {
        background-color: $theme-color-2;
        color: #fff;
      }
    }
    &:before {
      @include icomoon;
      content: '\1f51d';
      font-size: 10px;
    }
    &.left {
      margin-left: 0;
      &:before {
        transform: rotate(-90deg);
      }
    }
    &.right {
      margin-right: 0;
      &:before {
        transform: rotate(90deg);
      }
    }
  }
}
