@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.back-to-top {
  display: block;
  height: 32px;
  font-size: 18px;
  line-height: 32px;
  text-decoration: none;

  .site-footer & {
    float: right;
    padding-left: 15px;
    color: $lightgrey-1;

    @include mq_min_width(1400px) {
      position: absolute;
      right: 0;
      transform: translateX(100%);
    }
  }
}
