.list-articles.block.block-page.world-champions-tour {
  position: relative;
  margin: 40px 0;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: flex-start;
  @include mq_min_width($tablet-medium) {
    margin: 80px 0;
  }
  padding: 50px 0 40px;
  @include mq_min_width($desktop-medium) {
    padding: 80px 133px 80px 0;
  }
  &:before {
    content: '';
    background: $theme-color-1;
    width: 100vw;
    top: 0;
    height: 100%;
    z-index: 1;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    @include mq_min_width($desktop-medium) {
      left: auto;
      right: 0;
      transform: none;
    }
  }
  & > * {
    position: relative;
    z-index: 2;
  }
  .custom_generic_progressBar {
    @if not $is_mobile {
      display: none !important;
    } @else {
      z-index: 2;
      position: relative;
      bottom: auto;
      margin-top: 30px;
    }
  }
  .world-champions-tour-slider {
    overflow: visible;
    width: 100%;
    @include mq_min_width($desktop-medium) {
      margin-right: -10000px;
      width: 20000px;
      overflow: hidden;
    }
  }
  .la-slider {
    overflow: visible;
    width: 100%;
    @include mq_min_width($desktop-medium) {
      max-width: 1117px;
    }
  }
  .main-title-with-link {
    border: 0;
    margin-bottom: 28.5px;
    border-bottom: 1px solid #fff;
    padding-bottom: 15px;
    width: 100%;
    @include mq_min_width($tablet-medium) {
      width: calc(100% - 156px);
    }
    @include mq_min_width($desktop-small) {
      padding-bottom: 36.5px;
    }
    .main-title {
      float: none;
      text-transform: none;
      font-weight: 900;
      color: $textcolor-3;
      font-size: 30px;
      line-height: 44px;
      @include mq_min_width($desktop-small) {
        font-size: 40px;
        line-height: 60px;
      }
    }
    .link-view-all {
      display: none;
    }
  }
  .world-champions-tour__arrows-container {
    @include slider-arrows;
    margin: 0 0 28.5px;
    border-bottom: 1px solid #fff;
    align-items: flex-end;
    padding-left: 30px;
    padding-bottom: calc(15px + 10px);
    display: none;
    @include mq_min_width($tablet-medium) {
      display: flex;
    }
    @include mq_min_width($desktop-small) {
      padding-bottom: calc(36.5px + 10px);
    }
  }
  .slider-content {
    margin: 0;
    align-items: stretch;
    justify-content: flex-start;
    flex-wrap: nowrap;
    .la-item {
      border-radius: 5px;
      margin: 0;
      width: calc(100vw - 50px);
      max-width: 260px;
      background: #fff;
      transition: opacity 0.2s ease-in-out;
      &:not(.swiper-slide-visible) {
        opacity: 0.4;
        @include mq_min_width($desktop-medium) {
          & + .la-item:not(.swiper-slide-visible) {
            opacity: 0.4;
          }
        }
      }
      & > a {
        border-radius: 5px;
        position: relative;
        &:hover,
        &:active,
        &:focus {
          transform: scale(1.2);
          transition: transform 7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
        }
      }
      .at-illust,
      .gla-item-img,
      .la-item-img,
      .cx-lazy-load {
        height: 131px;
        width: 100%;
        min-height: 0;
        flex-shrink: 0;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        object-fit: cover;
        font-family: 'object-fit: cover;';
        transition: transform 1s cubic-bezier(0.25, 0.46, 0.45, 0.94);
      }
    }
    .la-item-content {
      display: flex;
      position: static;
      z-index: 1;
      text-align: left;
      flex-direction: column;
      padding: 22px 25px 20px;
      justify-content: flex-end;
      align-items: flex-start;
      border-bottom-left-radius: 6px;
      border-bottom-right-radius: 6px;
      height: calc(100% - 131px);
      & > * {
        position: relative;
        z-index: 1;
      }
      .la-item-tag {
        position: relative;
        background: none;
        top: auto;
        left: auto;
        padding: 0 55px 0 0;
        color: $textcolor-2;
        text-transform: none;
        font-size: 12px;
        font-family: $font-2;
        font-weight: 500;
        letter-spacing: 1.04px;
        line-height: 22px;
        display: block;
        margin-bottom: 14px;
        width: 100%;
        min-height: 32px;
        &:after {
          content: '';
          top: -11px;
          right: -17.5px;
          position: absolute;
          background-repeat: no-repeat;
          background-size: cover;
          background-position: 50% 50%;
          height: 45px;
        }
        &.topic_sial_canada_2018_gold_winner {
          &:after {
            background-image: image('visual/or.svg');
            width: 61px;
          }
        }
        &.topic_grand_prix_argent {
          &:after {
            background-image: image('visual/argent.svg');
            width: 62px;
          }
        }
        &.topic_sial_canada_2018_bronze_winner {
          &:after {
            background-image: image('visual/bronze.svg');
            width: 61px;
          }
        }
      }
      .la-item-title {
        font-weight: 900;
        font-family: $font-2;
        color: $textcolor-2;
        margin-bottom: 8px;
        letter-spacing: 1.04px;
        line-height: 26px;
        font-size: 18px;
        & > a {
          color: $textcolor-2;
        }
        span[class^='category-prix'] {
          display: none;
        }
      }
      .la-item-desc {
        margin-top: auto;
        font-family: $font-1;
        line-height: 27px;
        font-size: 15px;
        font-weight: 500;
        color: $lightgrey-2;
        strong:not(:first-of-type) {
          &:before {
            content: '\A';
            white-space: pre;
          }
        }
      }
      .la-item-btn {
        margin: 7px 0 0;
        .btn-primary {
          @extend %button-underline;
          font-size: 0;
          letter-spacing: 0;
          color: transparent;
          cursor: pointer;
          .icon {
            display: none;
          }
          &:before {
            color: $textcolor-5;
            letter-spacing: normal;
            font-size: 16px;
            html[lang='fr'] & {
              content: 'Voir plus';
            }
            html[lang='en'] & {
              content: 'See more';
            }
          }
          &:hover,
          &:active {
            &:after {
              width: 100%;
            }
          }
        }
      }
    }
  }
  .include_in_above_block {
    @extend %button;
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
    @include mq_min_width($tablet-medium) {
      margin-left: 0;
      margin-right: 0;
    }
    @include mq_min_width($desktop-medium) {
      margin-top: 50px;
    }
  }
}
