.tiles-list {
  clear: both;
  position: relative;
  z-index: 0;
  margin-top: 49px;
  margin-bottom: 49px;
  padding: 50px 0 80px;
  text-align: center;
  background-color: $lightgrey-3;

  .front &,
  .page-rubric & {
    background-color: $theme-color-1;
  }

  @include mq_min_width($tablet-medium) {
    margin-top: 85px;
    margin-bottom: 100px;
    padding-top: 70px;
  }

  &::after { // overflow background
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: -25px;
    display: block;
    height: 100%;
    width: 100vw;
    background-color: $lightgrey-3;

    .front &,
    .page-rubric & { // Hub-visiteur page
      background-color: $theme-color-1;
    }

    .page-rubric & { // Hub-visiteur page
      left: auto;
      right: -25px;
    }

    @include mq_min_width($tablet-medium) {
      left: 0;
    }
  }

  &__wrapper {
    display: block;
  }

  &__title {
    text-align: center;
    line-height: 60px;
    margin-bottom: 20px;
  }

  &__description {
    max-width: 920px;
    margin: 0 auto 40px;
    font-family: $font-1-alt;
    font-size: 18px;
    font-weight: 300;
    line-height: 30px;
    color: $textcolor-2;
    text-align: center;

    @include mq_min_width($tablet-medium) {
      font-size: 20px;
    }
  }

  &__link {
    @extend %button;
    width: auto;
    min-width: 190px;
    height: auto;
    margin-top: 48px;

    @include mq_min_width($tablet-medium) {
      margin-top: 50px;
    }
  }

  &__progressbar {
    position: absolute;
    bottom: 154px;
    display: block;
    width: 100%;
    height: 2px;
    background: #fff;

    @include mq_min_width($tablet-medium) {
      display: none;
    }

    &__btn,
    > span {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      transform-origin: left center;
      background: $theme-color-2;
    }
  }

  &__list {
    display: flex;

    @include mq_min_width($tablet-medium) {
      margin: -15px -17px;
      flex-wrap: wrap;
      justify-content: center;
    }

    &__wrapper {
      display: block;
    }
  }

  &__item {
    position: relative;
    z-index: 0;
    width: 213px;
    flex-shrink: 0;
    border-radius: 6px;
    overflow: hidden;

    @include mq_min_width($tablet-medium) {
      width: 362px;
      margin: 15px 17px;
    }

    &::before { // gradient mask shadow
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 0;
      background: linear-gradient(270deg, rgba(#24221d, 0) 0%, rgba(#24221d, .01) 12.23%, rgba(#231f1b, .74) 100%);
      pointer-events: none;

      .front & {
        background: linear-gradient(180deg, rgba(#24221d, 0) 0%, rgba(#231f1b, .66) 100%);
      }
    }

    &::after { // plus button
      @include icomoon;
      content: map_get($icon-list, 'plus');
      position: absolute;
      bottom: 0;
      right: 0;
      display: block;
      width: 25px;
      height: 25px;
      font-size: 12px;
      font-weight: 400;
      line-height: 25px;
      text-align: center;
      color: $theme-color-2;
      background: #fff;
      border-top-left-radius: 16px;
      pointer-events: none;

      @include mq_min_width($tablet-medium) {
        width: 32px;
        height: 32px;
        line-height: 32px;
      }
    }

    &__img {
      position: absolute;
      z-index: -1;
      top: 50%;
      left: 50%;
      display: block;
      min-width: 100%;
      min-height: 100%;
      background: center center / cover no-repeat;
      transform: translate(-50%, -50%);
      object-fit: cover;
      font-family: 'object-fit: cover;';
      @include transition(7s, transform);

      .tiles-list__item:hover & {
        transform: translate(-50%, -50%) scale(1.1);
      }
    }

    &__title {
      position: relative;
      z-index: 1;
      display: block;
    }

    &__link {
      position: relative;
      display: block;
      width: 100%;
      height: 185px;
      padding: 45px 20px 20px;
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      line-height: 35px;
      text-align: left;
      text-transform: none;
      color: #fff;
      border: 0;
      border-radius: 6px;
      background: center center / contain no-repeat;
      background-size: 100%;
      box-shadow: 0 4px 9px 0 rgba(#000, 0.13);
      overflow: hidden;

      &:active,
      &:focus,
      &:hover {
        background: none;
      }
    }

    &__icon {
      position: relative;
      z-index: 1;
      display: block;
      height: 35px;
      margin-bottom: 12px;
      font-size: 35px;
    }
  }
}
