@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.social-networks {
  .site-footer & {
    margin-left: auto;
    align-self: flex-end;
    margin-top: 30px;
    @include mq_min_width($desktop-small) {
      margin-top: 0;
      margin-bottom: 14px;
    }
  }

  .ql {
    &-list {
      display: flex;
      align-items: center;

      .site-footer & {
        justify-content: space-between;
        max-width: 268px;
        margin: 0 auto;
        @include mq_min_width($desktop-small) {
          justify-content: flex-start;
          max-width: none;
          margin: 0;
        }
      }

      @include mq_min_width($tablet-medium) {
        float: right;
        .site-footer & {
          float: none;
        }
      }
    }

    &-item {
      float: left;

      + .ql-item {
        margin-left: 15px;

        @include mq_min_width($tablet-medium) {
          margin-left: 7px;
        }
      }
    }
  }

  .btn-primary {
    font-size: 40px;
    padding: 0;
    color: currentColor;
    background: none;
    border: 0;

    @include mq_min_width($tablet-medium) {
      font-size: 32px;
    }

    &:hover,
    &:focus {
      color: #fff;
    }

    .txt-btn {
      display: none;
    }

    .icon {
      margin: 0;
    }
  }
}
