.propale {
  width: 100%;
  margin: 37px auto 40px;

  &__bgFallback,
  &__bgFallback > * {
    position: absolute;
    top: 0;
    left: 50%;
    display: block;
    width: 100%;
    height: 100%;
    text-align: center;
    overflow: hidden;
    transform: translateX(-50%);
    z-index: -1;
    background: center center / cover no-repeat;

    & > * {
      position: absolute;
      top: 50%;
      left: 50%;
      right: 0;
      display: none;
      max-width: none;
      min-width: 100%;
      min-height: 100%;
      margin: 0 auto;
      transform: translate(-50%, -50%);
    }
  }

  &__wrapper {
    position: relative;
    margin: auto;
    display: flex;

    @include mq_min_width($tablet-small) {
      flex-direction: row-reverse;
    }
  }

  &__img {
    margin-left: auto;
    margin-top: 20px;
    margin-right: 20px;
    display: none;

    @include mq_min_width($tablet-small) {
      display: block;
    }
  }

  .deadline {
    &__block {
      width: 100%;
      height: 230px;
      margin-right: auto;
      margin-left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      padding: 50px 32px 30px 29px;

      @include mq_min_width($tablet-small) {
        width: 230px;
      }

      > p {
        color: $textcolor-2;
        font-family: $font-2;
        font-size: 20px;
        font-weight: 900;
        line-height: 1.5;
      }

      &--registration {
        background-color: rgba($textcolor-2, 0.7);

        p {
          color: #fff;
          text-align: center;
        }
      }
      &--participate {
        background: $theme-color-1 image('visual/sial-propale-bg.svg') bottom right no-repeat;
        text-align: left;

        .btn-primary {
          @extend %button;
          margin-top: 12px;
          align-self: flex-start;
        }
      }
    }
  }
}