@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

[class^='btn'], .btn {
  font-family: inherit;

  .txt-btn {
    vertical-align: initial;
  }
}

%button {
  display: inline-block;
  padding: 9px 26px 10px;
  font-family: $font-1;
  font-size: 16px;
  font-weight: 500;
  line-height: 26px;
  text-align: center;
  text-transform: none;
  text-decoration: none;
  color: #fff;
  background: $textcolor-2;
  border: 0;
  border-radius: 23px;
  user-select: none;
  outline: 0;
  &:hover {
    background: $textcolor-1;
  }
}

%button-invert {
  @extend %button;
  color: $textcolor-2;
  background: #fff;

  &:hover {
    color: $textcolor-1;
    background: #fff;
  }
}

%button-underline {
  position: relative;
  display: inline-block;
  padding: 2px 0;
  font-family: $font-1;
  font-size: 16px;
  font-weight: 500;
  line-height: 26px;
  text-align: left;
  text-transform: none;
  text-decoration: none;
  color: currentColor;
  background: none;
  border: 0;
  transition: none;
  overflow: hidden;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 35px;
    max-width: 100%;
    height: 0;
    display: block;
    border-bottom: 1px solid $theme-color-2;
    @include transition(.3s, width);
  }

  &.is-active,
  &:hover {
    &::after {
      width: 100%;
    }
  }
}
