@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

body.node1316971 {
  .more-infos-contact__inner {
    margin: auto;
  }

  .main-title {
    text-align: center;
  }
  .intro p {
    text-align: center;
    padding: 0 8.3334%;
  }
  .list-articles {
    &::before {
      max-height: initial !important;
      padding-bottom: 20%;

      @include mq_min_width($tablet-medium) {
        padding-bottom: 15%;
      }
      @include mq_min_width($desktop-medium) {
        padding-bottom: 10%;
      }
    }

    .grid-la-list {
      padding-bottom: 42px;
      margin-bottom: 0;

      .gla {
        &-item-content {
          padding: 30px;
        }

        &-item-title {
          margin-bottom: 15px;
        }

        &-item-btn {
          margin-top: 18px;
        }

        &-item-img,
        &-item .cx-lazy-load,
        &-item .gla-item-img,
        &-item .la-item-img,
        &-item .at-illust {
          @include mq_min_width($desktop-small) {
            min-height: 217px;
          }
        }

        &-item {
          margin-bottom: 10px;
        }
      }
    }

    @include mq_min_width(1141px) {
      .cxp-pagination .grid-la-list .gla-item {
        width: calc((100% - 3 * 16px) / 3);

        &:nth-child(4n + 1) {
          margin-left: 10px;
        }

        &:nth-child(3n + 1),
        &:nth-child(1) {
          margin-left: 0;
        }
      }
    }
  }
}

