.cc-cookies {
  position: fixed !important;
  left: 0;
  bottom: 0;
  top: auto;
  border-top: 1px solid rgba($lightgrey-1, 0.6);
  padding: 15px;
  font-size: 14px;
  .inside {
    text-align: center;
  }
  .cc-cookie-link {
    &:after {
      content: '\A';
      white-space: pre;
    }
  }
  .cc-cookie-accept {
    width: auto;
    @extend %button;
    display: inline-block !important;
    margin: 12.5px 0 0 !important;
  }
}
