@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.home-events {
  position: relative;
  display: block;
  padding-top: 48px;
  padding-bottom: 60px;
  @include clearfix;

  @include mq_min_width($tablet-medium) {
    padding-top: 70px;
    padding-bottom: 80px;
  }

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    right: -25px;
    width: 100vw;
    height: 100%;
    background-color: $lightgrey-3;

    @include mq_min_width($tablet-medium) {
      right: 107px;
    }
  }

  .list-articles {
    margin: 0;

    @include mq_min_width($desktop-small) {
      float: left;
      width: percentage(930px / $wrapper-size);
    }
  }

  .edito {
    height: 440px;
    max-width: 270px;
    margin: 0 auto !important; // Shame !
    padding: 90px 0 0 30px;
    color: #fff;
    background: rgba($darkgrey-1, .5) image('visual/conferences-bg.jpg') center center / cover no-repeat;
    border-radius: 6px;
    overflow: hidden;

    @include mq_min_width($desktop-small) {
      float: right;
      width: percentage(290px / $wrapper-size);
      height: 500px;
      margin-top: 90px !important; // Confess !
      padding-top: 120px;
    }

    @if $is_mobile {
      padding: 0 30px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .block-title {
      display: none;
    }

    p {
      margin: 0;
      font-family: $font-2;
      font-size: 36px;
      font-weight: 900;
      line-height: 45px;
    }

    .lien2 {
      @extend %button;
      margin-top: 33px;
    }
  }
}
