.site-banner .global-search-form {
  .gsf-fields {
    background: #fff;
    height: 0;
    left: 0;
    overflow: hidden;
    position: fixed;
    transition: height .4s ease-in-out;
    width: 100%;
    z-index: 50;
    right: auto;
    display: flex !important;
    flex-direction: column;
    justify-content: center;
    padding: 0 30px;
    flex-wrap: nowrap;
    align-items: center;
    top: 60.6px;
    @if $is_mobile {
      padding: 0 15px;
      background: $lightgrey-3;
    }
    @include mq_min_width($tablet-medium) {
      top: 114.2px;
    }
    @include mq_min_width($desktop-small) {
      top: 105.8px;
    }
    @include mq_min_width($desktop-medium) {
      padding: 0 20%;
    }
    @if $is_mobile {
      top: 62px !important;
    }
    .gsf-input-line {
      flex: 0 1 auto;
      border-radius: 0;
      border-bottom: 1px solid $lightgrey-1;
      flex-shrink: 0;
      flex-grow: 0;
      width: 100%;
      max-width: 800px;
    }
    .gsf-input {
      @if $is_mobile {
        background: transparent !important;
      }
      font-family: $font-2;
      font-weight: 900;
      color: $textcolor-3;
      font-size: 26px;
      line-height: 45px;
      padding: 0 0 10px;
      @include mq_min_width($tablet-small) {
        padding-bottom: 15px;
        font-size: 30px;
        line-height: 45px;
        padding-left: 19.5px;
        padding-right: 19.5px;
      }
      @include mq_min_width($tablet-medium) {
        padding-bottom: 19.5px;
        line-height: 60px;
        font-size: 42px;
      }
      @include placeholder {
        color: $lightgrey-2 !important;
      }
    }
    .gsf-submit-line {
      border-radius: 0;
      flex-shrink: 0;
      flex-grow: 0;
      margin-left: 0;
      margin-right: 0;
      align-items: flex-start;
      justify-content: flex-start;
      width: 100%;
      max-width: 800px;
      flex-wrap: wrap;
      margin-top: 20px;
      @include mq_min_width($tablet-medium) {
        justify-content: space-between;
        margin-top: 40px;
      }
      &:before {
        font-weight: 600;
        order: 1;
        content: 'Example:';
        html[lang='fr'] & {
          content: 'Exemple :';
        }
      }
      &:after {
        order: 2;
        margin-left: 7px;
        content: 'sector grocery...';
        @include mq_min_width($tablet-small) {
          content: 'sector grocery, program 2030, exhibitor...';
        }
        html[lang='fr'] & {
          content: 'secteur épicerie...';
          @include mq_min_width($tablet-small) {
            content: 'secteur épicerie, programme 2030, exposant...';
          }
        }
      }
      .gsf-submit-btn {
        width: 100%;
        padding: 0;
        display: block;
        text-align: left;
        margin-top: 30px;
        order: 3;
        margin-left: auto;
        @if $is_mobile {
          background: transparent !important;
        }
        @include mq_min_width($tablet-medium) {
          width: auto;
          margin-top: 0;
          display: inline-block;
          padding: 9px 26px 10px;
          font-family: $font-1;
          font-size: 16px;
          font-weight: 500;
          line-height: 26px;
          text-align: center;
          text-transform: none;
          text-decoration: none;
          color: #fff;
          background: $textcolor-2;
          border: 0;
          border-radius: 23px;
          user-select: none;
          &:hover {
            background: $textcolor-1;
          }
        }
        .gsf-submit-text {
          display: inline-block;
          padding: 10px 26px 8px;
          font-family: $font-1;
          font-weight: 500;
          line-height: 26px;
          text-align: center;
          text-transform: none;
          text-decoration: none;
          background: $textcolor-2;
          border: 0;
          border-radius: 23px;
          user-select: none;
          font-size: 0;
          letter-spacing: 0;
          color: transparent;
          @include mq_min_width($tablet-medium) {
            display: none;
          }
          &:before {
            color: #fff;
            font-size: 16px;
            letter-spacing: normal;
            content: 'Search';
            html[lang='fr'] & {
              content: 'Rechercher';
            }
          }
          &:hover {
            background: $textcolor-1;
          }
        }
        &:after {
          @include mq_min_width($tablet-medium) {
            content: 'Search';
            html[lang='fr'] & {
              content: 'Rechercher';
            }
          }
        }
      }
    }
  }
  &.is-visible {
    .gsf-trigger {
      .icon-search {
        &:before {
          content: '\271a';
          transform: rotate(45deg);
          display: inline-block;
        }
      }
    }
    .gsf-fields {
      height: calc(100% - 60.6px);
      @include mq_min_width($tablet-medium) {
        height: calc(100% - 114.2px);
      }
      @include mq_min_width($desktop-small) {
        height: calc(100% - 105.8px);
      }
      @if $is_mobile {
        height: calc(100% - 62px) !important;
        margin: 0;
        padding: 0 15px;
      }
    }
  }
}
