.more-infos-contact {
  max-width: 984px;
  margin: 80px auto 57px;
  font-family: $font-2;
  font-weight: 500;
  color: $lightgrey-2;
  body.article_list & {
    margin: 40px auto 57px;
  }
  @include mq_min_width($desktop-small) {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__title {
    font-weight: 900;
    color: $textcolor-3;
    margin: 0 auto;
    max-width: 415px;
    font-size: 30px;
    line-height: 44px;
    @include mq_min_width($desktop-small) {
      width: 47.36%;
      margin: 0;
      max-width: none;
      font-size: 40px;
      line-height: 65px;
    }
    &:before {
      @include icomoon;
      content: '\1f4ac';
      font-size: 95px;
      color: $lightgrey-1;
      display: block;
      margin-bottom: 30px;
    }
  }
  &__inner {
    margin: 30px auto 0;
    max-width: 415px;
    @include mq_min_width($desktop-small) {
      width: 42.17%;
      margin: 0;
      max-width: none;
    }
    text-align: center;
    box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.13);
    padding: 38px 15px 33px;
  }
  &__image {
    width: 88px;
    height: 88px;
    margin: 0 auto;
    border-radius: 50%;
    overflow: hidden;
  }
  &__name {
    font-size: 18px;
    font-weight: 900;
    color: $textcolor-2;
    margin-top: 21px;
    & ~ p {
      margin-top: 11px;
      line-height: 30px;
    }
  }
  a {
    text-decoration: none;
  }
  .link-view-all {
    @extend %button;
    margin-top: 20px;
    margin-bottom: 24px;
  }
}
