@import '../includes/variables';
@import '../includes/mixins';

.site-banner {
  position: fixed;
  z-index: 20;
  top: 0;
  right: 0;
  left: 0;
  padding: 0;
  color: currentColor;
  background: #fff;
  border-bottom: 1px solid #e3e9ea;

  @if $is_mobile {
    height: 62px;
    border: 0;
  }

  &::after { // top-header background
    content: '';
    display: none;
    height: 55.2px;
    width: calc(100vw - 387px);
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    opacity: 0.35;
    background-color: $lightgrey-5;

    @include mq_min_width($tablet-medium) {
      display: block;
    }

    @include mq_min_width($desktop-small) {
      height: 37px;
    }
  }

  &.is-stuck {
    padding: 0;
  }

  .inside {
    max-width: 1360px;
    padding-top: 0;
    @if $is_mobile {
      .body-mobile & {
        box-shadow: none;
        display: flex;
        height: 100%;
        align-items: center;
        justify-content: space-between;
        padding: 0 15px !important;
        @include mq_min_width($tablet-small) {
          padding: 0 24.5px !important;
        }
      }
    }
    & > .sb-menu-trigger {
      @if $is_mobile {
        display: none;
      }
    }
  }
  @if $is_mobile {
    .custom-trigger-menu-mobile {
      position: relative;
      height: 16px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: stretch;
      margin-top: 0;
      transition: margin-top 0.55s ease;
      .custom-menu-mobile-open & {
        margin-top: 3px;
      }
      span {
        display: block;
        width: 20px;
        height: 2px;
        position: relative;
        background: $textcolor-2;
        border-radius: 3px;
        z-index: 1;
        transform-origin: 4px 0;
        transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1.0), opacity 0.55s ease;
        .custom-menu-mobile-open & {
          opacity: 1;
          transform: rotate(45deg) translate(0, 3px);
        }
        &:nth-child(2) {
          .custom-menu-mobile-open & {
            opacity: 0;
            transform: rotate(0deg) scale(0.2, 0.2);
          }
        }
        &:nth-child(3) {
          transform-origin: 0 100%;
          .custom-menu-mobile-open & {
            transform: rotate(-45deg) translate(0, -1px);
          }
        }
      }
    }
  }
}

.is-contentPusher {
  display: none;
}
