@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.global-search-form {
  .site-banner & {
    position: relative;
    float: right;
    right: auto;
    bottom: auto;
    margin: 17.6px 0;
    @include mq_min_width($desktop-small) {
      margin: 8px 0;
    }
    @if $is_mobile {
      float: none;
      margin: 0;
      top: auto;
      background: transparent;
      width: auto;
      height: auto;
    }
  }
}

.gsf {
  &-trigger {
    .site-banner & {
      height: 20px;
      padding: 0 20px;
      font-size: 17px;
      line-height: 20px;
      color: $theme-color-2;
      border-left: 1px solid rgba($lightgrey-1, .3);

      @if $is_mobile {
        height: auto;
        line-height: normal;
        border-right: 1px solid rgba($lightgrey-1, .3);
        border-left: 0;
        padding-left: 0;
        margin-right: 15px;
        width: auto;
        padding-right: 15px;
        @include mq_min_width($tablet-small) {
          padding-right: 20px;
          margin-right: 20px;
        }
      }

      @include mq_min_width(1400px) {
        padding-right: 0;
      }
    }

    &:hover,
    &:focus,
    .global-search-form.is-visible & {
      .site-banner & {
        background: none;

        .icon {
          color: currentColor;
          transform: scale(1.1);
        }
      }
    }

    .icon {
      .site-banner & {
        display: inline-block;
        vertical-align: middle;
        color: currentColor;
        @include transition(.3s, transform)
      }
    }
  }

  &-fields {
    .site-banner & {
      background: $theme-color-2;
    }
  }

  &-input-line {
    .site-banner & {
      @include border-left-radius(6px);
      overflow: hidden;
    }
  }

  &-submit {
    &-line {
      .site-banner & {
        @include border-right-radius(6px);
        overflow: hidden;
      }
    }

    &-btn {
      .site-banner & {
        color: $theme-color-2;
        background: #fff;
      }

      &:hover {
        .site-banner & {
          color: $theme-color-2;
          background: #fff;
        }
      }
    }
  }
}
