@import '../includes/variables';
@import '../includes/mixins';

.site-footer {
  clear: both;
  color: $lightgrey-1;
  background-color: $darkgrey-1;
  overflow: hidden;
  .inside {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-width: $wrapper-size + 30px;
    padding: 49px 25px 0;
    @include mq_min_width($tablet-medium) {
      flex-direction: row;
      padding-left: 15px;
      padding-right: 15px;
    }
    > * {
      display: block;
      width: 100%;
    }
    & > div:not(.footer-bottom) {
      @include mq_min_width($desktop-small) {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
      }
    }
  }
}
