@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

body.matchmaking {
  h1,
  .la-main-title {
    @extend .main-title;
    text-align: center;
  }

  .main-title-with-link {
    border: 0;
    margin-top: 80px;
    display: flex;

    h2 {
      margin: 0 auto;
    }
  }

  #zone1 .block.edito {
    margin: 15px auto 30px;

    @include mq_min_width($desktop-small) {
      width: 66.6667%;
    }

    p {
      color: $lightgrey-2;
      font-size: 20px;
      font-weight: 600;
      line-height: 1.55;

      &:first-of-type {
        text-align: center;
      }

      &:not(:first-of-type) {
        padding: 0 10px;
        display: inline-block;
        width: 100%;
        text-align: center;
        order: 2;
        margin-bottom: 30px;
        margin-left: auto;
        margin-right: auto;

        @include mq_min_width($tablet-medium) {
          width: calc(47% - 10px);
          margin-top: 20px;
          margin-bottom: 10px;
          margin-left: 0.65em;
          margin-right: 0.65em;

          &:last-of-type {
            text-align: left;
          }
        }
      }

      &:nth-of-type(2) {
        margin-bottom: 10px;
        @include mq_min_width($tablet-medium) {
          text-align: right;
        }
      }
    }

    .lien4 {
      @extend %button;
    }
  }

  #zone2 .block.edito {
    h2 {
      @extend .main-title;
      border: 0;
      margin: 0 0 24px;
      order: 5;

      @include mq_min_width($tablet-medium) {
        font-size: 40px;
      }
    }

    p {
      font-size: 20px;
      font-weight: 300;
      line-height: 1.5;
      margin: 0 0 24px;
      order: 3;

      &:nth-of-type(2) {
        border-bottom: 1px solid $lightgrey-1;
        padding-bottom: 20px;
      }
    }

    .inside {
      padding: 15px;
      display: flex;
      flex-direction: column-reverse;
      @include clearfix;

      @include mq_min_width($tablet-medium) {
        display: block;
        & > img {
          float: right;
          width: 40%;
        }
      }
      @include mq_min_width($desktop-small) {
        padding: 78px 131px;
        & > img {
          width: 427px;
          height: 427px;
        }
      }
    }

    h2,
    p,
    ul {
      @include mq_min_width($tablet-medium) {
        float: left;
        width: calc(50% + 15px);
      }
      @include mq_min_width($desktop-medium) {
        float: left;
        width: calc(57% + 15px);
      }
    }

    img {
      order: 4;
      margin-bottom: 10px;
    }

    .style2 {
      color: $lightgrey-2;
      font-family: $font-2;
      font-size: 11px;
      font-weight: 500;
      letter-spacing: 2px;
      line-height: 3.3;
      text-transform: uppercase;
    }

    &:first-of-type {
      background-color: $lightgrey-3;

      img {
        max-height: 427px;
        margin: auto;

        @include mq_min_width($tablet-medium) {
          margin:0 0 70px;
        }
        @include mq_min_width($desktop-small) {
          margin-left: -63px;
        }
      }

      p {
        &:nth-of-type(2) {
          order: 2;
          margin-bottom: 0;

          @include mq_min_width($tablet-medium) {
            margin-bottom: 20px;
          }

        }
        &:last-of-type {
          order: 1;
        }

        &:nth-of-type(4),
        &:nth-of-type(5),
        &:nth-of-type(6) {
          display: none;
        }
        &:nth-of-type(3) {
          width: 16%;
          min-width: 132px;
          order: 1;
        }
      }

      @include mq_min_width($desktop-small) {
        margin-left: -63px;
        margin-right: -63px;
      }

      figure {
        width: 135px;
        height: 47px;
        float: left;
        margin-top: -4px;
        margin-left: 5px;

        @include mq_min_width($tablet-small) {
          position: absolute;
          left: 150px;
          margin-top: -15px;

          &:last-of-type {
            left: 290px;
          }
        }

        @include mq_min_width($desktop-small) {
          position: initial;
          margin: -5px 0 0 10px;
          left: initial;
        }
      }
    }

    &:last-of-type {
      position: relative;
      margin-bottom: 80px;

      &::before {
        content: '';
        position: absolute;
        width: 100vw;
        height: 100%;
        top: 0;
        background: $lightgrey-3;
        z-index: -1;
        left: 50%;
        transform: translateX(-50%);

        @include mq_min_width($desktop-small) {
          transform: initial;
          left: initial;
          right: 0;
        }
      }

      p {
        font-size: 16px;
      }

      h2,
      p,
      ul {
        padding-right: 3%;
      }

      ul {
        order: 1;
        margin-top: 0;
      }

      @include mq_min_width($desktop-small) {
        min-height: 600px;

        img {
          height: 412px;
          width: 673px;
          position: absolute;
          right: -103px;
        }
        .inside {
          padding: 78px 131px 78px 0;
        }
      }
    }
  }

  .list-articles {
    margin-top: 0;

    .grid-la-list {
      flex-direction: column;
      margin-left: 0;

      @include mq_min_width($tablet-medium) {
        flex-direction: row;
        justify-content: flex-start;
      }
    }

    .gla-item {
      background: none;
      box-shadow: none;
      border-radius: 0;
      text-align: center;
      margin: 10px auto;
      max-width: 300px;
      width: 100%;

      @include mq_min_width($tablet-medium) {
        min-width: 300px;
        width: calc(100% / 2 - 30px);
      }

      @include mq_min_width($desktop-medium) {
        width: calc(100% / 4 - 25px);

        &:first-child {
          margin-left: 0;
        }
        &:last-child {
          margin-right: 0;
        }
      }

      img {
        height: 289px;
        width: 289px;
        border-radius: 0;
      }

      &-content {
        text-align: center;
        padding: 30px 10px 10px;
        height: auto;
        background: none;
      }

      &-title {
        color: $textcolor-2;
      }
    }
  }
}
