@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

/* Page /Infos-Pratiques/Reserver-votre-hebergement-pour-SIAL-Paris-2020 */
body.node1316963 {
  .article {
    &-intro {
      width: 83.3334%;
      margin-left: auto;
      margin-right: auto;
    }
    &-content {
      > * {
        width: 100%;
      }

      iframe {
        margin-bottom: 20px;
        margin-left: 0 !important;
        width: 100%;
      }

      .lien2 {
        @extend %button;
        position: relative;
      }

      p {
        font-size: 16px;

        &:first-of-type { display: none; }

        &:nth-of-type(2) {
          @extend %content-h2Like;
          text-align: left;
          margin-bottom: 10px;
        }

        a { text-decoration: none; }

        &:nth-of-type(3) {
          line-height: 1.625;
        }

        &:nth-of-type(3),
        &:nth-of-type(4),
        &:nth-of-type(5),
        &:nth-of-type(6),
        &:nth-of-type(7) {
          margin-top: 0;
          margin-bottom: 5px;
        }
        &:nth-of-type(4) {
          font-weight: 700;
          margin-top: 10px;
        }
        &:nth-of-type(7) {
          margin-top: 15px;

          @include mq_min_width($tablet-medium) {
            position: absolute;
            bottom: 12%;
            right: 10%;
            width: auto;
          }
        }

        &:nth-of-type(8),
        &:nth-of-type(9) { opacity: 0; }
      }
    }
  }
}
