@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.page-highlight {
  position: relative;
  z-index: 0;
  margin: 60px 0 0;

  @include mq_min_width($desktop-small) {
    display: flex;
    margin: 80px 0 0;
    padding: 80px 0;
    align-items: stretch;
    justify-content: flex-start;
  }

  @if $is_mobile {
    max-width: 610px;
    margin-left: auto;
    margin-right: auto;
  }

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 50%;
    bottom: -649px;
    display: none;
    width: 100vw;
    min-height: 100%;
    background-color: $lightgrey-3;
    transform: translateX(-50%);

    @include mq_min_width($desktop-small) {
      display: block;
    }
  }

  .block.edito {
    display: flex;
    flex-direction: column;
    margin: 0 0 36px;
    flex-shrink: 0;
    background-color: $lightgrey-3;

    @include mq_min_width($desktop-small) {
      width: percentage(673px / $wrapper-size);
      min-height: percentage(411px / $wrapper-size);
      margin-bottom: 0;
      background-color: #fff;
    }

    .inside {
      height: 100%;
      font-size: 20px;
      line-height: 30px;
      padding: 0 0 40px;

      @if $is_mobile {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
      }

      @include mq_min_width($tablet-medium) {
        padding-right: percentage(59px / 673px);
        padding-bottom: 0;
      }

      > *:not(img) {
        margin-left: 25px;
        margin-right: 25px;

        @include mq_min_width($tablet-medium) {
          margin-left: percentage(340px / 673px);
        }
      }
      @if $is_mobile {
        & > *:not(img):not(h2) {
          order: 3;
        }
      }
    }

    img {
      min-width: 100%;
      max-width: 100%;
      height: 268px;
      object-fit: cover;
      font-family: 'object-fit: cover;';
      @if $is_mobile {
        order: 1;
      }
      @include mq_min_width($tablet-medium) {
        float: left;
        width: percentage(290px / 673px);
        min-width: 0;
        height: 100%;
      }
    }

    h2 {
      @extend .main-title;
      margin-top: 32px;
      font-size: 30px;
      line-height: 40px;
      @if $is_mobile {
        order: 2;
      }
      @include mq_min_width($tablet-medium) {
        font-size: 40px;
        line-height: 60px;
        width: 56.90936%;
        float: right;
        padding-left: 30px;
        margin-left: 0 !important;
        margin-right: 0 !important;
        margin-top: 20px !important;
        margin-bottom: 15px;
      }
    }

    @include mq_min_width($tablet-medium) {
      .inside > *:not(img):not(h2) {
        width: 56.90936%;
        margin-left: 0 !important;
        margin-right: 0 !important;
        margin-top: 0 !important;
        float: right;
        padding-left: 30px;
        &:nth-last-child(1) {
          margin-bottom: 20px;
        }
      }
    }

    .btn-primary {
      @extend %button;
      margin-top: 27px;
    }
  }
}
