@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

#cxpmClientAccountWidget {
  float: right;
  display: none;
  margin: 17.2px 0;
  font-family: $font-1-alt !important;
  font-size: 14px;
  font-weight: 300;

  @include mq_min_width($tablet-medium) {
    display: block;
  }

  @include mq_min_width($desktop-small) {
    margin: 8px 0;
  }

  @if $is_mobile {
    display: none !important;
  }

  .widget-text {
    float: right;
    margin: 0;
    padding: 0 20px !important;
    line-height: 20px;
    border-left: 1px solid rgba($lightgrey-1, .3);

    span a {
      font-size: 14px !important;
    }

    i {
      display: none;
    }
  }

  .widget-my-selection {
    float: left;
    line-height: 20px !important;

    .widget-icon {
      padding: 0 20px;
      color: $theme-color-2;

      i {
        font-size: 12px;
        color: currentColor;
      }

      &::after { // FIXME: Look Mom, no hands !
        content: 'My selection';
        color: $textcolor-1;

        html[lang='fr'] & {
          content: 'Ma sélection';
        }
      }

      .number {
        display: none;
      }
    }
  }

  .see-all-selection a {
    @extend %button;
    background: $textcolor-2 !important;
  }
}
