@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.home-social-networks {
  text-align: center;
  margin-top: 90px;
  margin-bottom: 70px;

  &__title {
    font-size: 42px;
  }

  &__description {
    font-size: 20px;
  }

  &__list {
    display: flex;
    margin-top: 19px;
    justify-content: center;
  }

  &__item {
    display: block;
    height: 49px;
    width: 49px;
    font-size: 49px;
    line-height: 49px;
    text-align: center;
    text-decoration: none;
    color: $lightgrey-1;
    border-radius: 50%;
    @include transition;

    &:hover {
      color: $lightgrey-1;
      transform: scale(1.1);
    }

    & + & {
      margin-left: 11px;
    }
  }
}
