@import '../includes/variables';
@import '../includes/mixins';

.wrapper,
.site-wrapper {
  max-width: $wrapper-size + (15px * 2); // wrapper + paddings
}

.site-wrapper,
.global-wrapper > .wrapper,
#main > .wrapper {
  margin-bottom: 0;
  padding: 0 25px;
  background: none;
  box-shadow: none;

  @include mq_min_width($tablet-medium) {
    padding: 0 15px;
  }
}

.global-wrapper {
  padding-top: 60.6px;
  padding-bottom: 40px;
  overflow-x: hidden;

  @include mq_min_width($tablet-medium) {
    padding-top: 114.2px;
    padding-bottom: 50px;
  }

  @include mq_min_width($desktop-small) {
    padding-top: 105.8px;
  }
  @if $is_mobile {
    padding-top: 62px !important;
  }
}
