@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.social-sharing {
  body.article-program &,
  body.sial-innovation & {
    margin: 0;
    border: 0;
    order: 2;
    background: $lightgrey-3;
    padding: 25px 26px;
    flex-grow: 0;
    flex-shrink: 0;
    text-align: center;
    .sr-title {
      color: $lightgrey-2;
      font-family: $font-2;
      font-size: 12px;
      font-weight: 500;
      letter-spacing: 2.24px;
      text-transform: uppercase;
      line-height: 22px;
      margin: 5px 0 13px;
    }
    .sr-list {
      padding: 0;
      margin: 0;
      display: flex;
      align-items: flex-start;
      justify-content: center;
    }
    .sr-item {
      padding: 0;
      margin: 0;
      border: 0;
      width: 45px;
      height: 45px;
      position: relative;
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        border: 0;
        padding: 0;
        width: 100%;
        height: 100%;
        user-select: none;
        .icon,
        .sr-item-txt {
          display: none;
        }
        &:before {
          @include icomoon;
          color: $lightgrey-1;
          font-size: 45px;
          transition: color 0.2s ease-in-out;
        }
        &.sr-link-mail {
          &:before {
            content: '\2709';
          }
        }
        &.sr-link-facebook {
          &:before {
            content: '\e900';
          }
        }
        &.sr-link-twitter {
          &:before {
            content: '\e903';
          }
        }
        &.sr-link-linkedin {
          &:before {
            content: '\e902';
          }
        }
        &:hover,
        &:active {
          &:before {
            color: $theme-color-2;
          }
        }
      }
      &:not(:first-of-type) {
        margin-left: 12px;
      }
    }
  }
}

.article-title {
  @extend .edito-custom;
  body.sial-innovation & {
    margin: 0 0 40px;
    @include mq_min_width($tablet-medium) {
      margin: 0 0 80px;
    }
    @include mq_min_width($desktop-medium) {
      margin: -45px 0 80px;
    }
    & > .inside {
      @include mq_min_width($desktop-small) {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
      }
      .at-content {
        margin: 0;
        @include mq_min_width($desktop-small) {
          width: calc(51.2% - 30px);
          order: 1;
        }
        @include mq_min_width($desktop-medium) {
          padding-top: 56px;
        }
        .article-intro {
          width: 100%;
          @include mq_min_width($desktop-medium) {
            width: calc(100% - 77px);
          }
          margin: 0;
          font-size: 20px;
          ul,
          ol {
            padding-left: 20px;
          }
        }
        .blocs-starts {
          width: 100%;
          @include mq_min_width($desktop-medium) {
            width: calc(100% + 77px);
          }
          margin-top: 27px;
          @include mq_min_width($tablet-medium) {
            display: flex;
            align-items: stretch;
            justify-content: space-between;
          }
          .social-sharing {
            width: 100%;
            @include mq_min_width($tablet-medium) {
              width: calc(50% - 15px);
            }
            @include mq_min_width($desktop-small) {
              padding: 25px 15px;
            }
            @include mq_min_width($desktop-medium) {
              padding: 25px 26px;
            }
            & + .award-ceremony {
              margin-top: 30px;
              @include mq_min_width($tablet-medium) {
                margin-top: 0;
              }
            }
          }
          .award-ceremony {
            padding: 25px 26px;
            font-family: $font-2;
            font-size: 20px;
            font-weight: 900;
            line-height: 30px;
            order: 1;
            background: $theme-color-1;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-grow: 0;
            flex-shrink: 0;
            overflow: hidden;
            position: relative;
            margin-left: auto;
            margin-right: auto;
            width: 100%;
            @if $is_mobile {
              max-width: 320px;
            }
            @include mq_min_width($tablet-small) {
              width: calc(50% - 15px);
              min-width: 290px;
            }
            @include mq_min_width($tablet-medium) {
              margin-left: 0;
              margin-right: 0;
              min-width: 0;
            }
            &:before {
              @include icomoon;
              content: '\1f3c6';
              position: absolute;
              bottom: 0;
              left: 0;
              font-size: 115px;
              color: $theme-color-1-dark;
            }
          }
          .award-ceremony__title {
            position: relative;
            z-index: 1;
          }
        }
        .at-main-title {
          border-bottom: 1px solid $lightgrey-1;
          margin-left: 0;
          width: 100%;
          @include mq_min_width($desktop-medium) {
            width: calc(100% - 77px);
          }
          a {
            display: block;
          }
          .hallb {
            font-family: $font-3;
            font-size: 12px;
            font-weight: 300;
            letter-spacing: 0.89px;
            position: relative;
            padding-left: 39px;
            display: inline-block;
            margin-right: 100%;
            white-space: nowrap;
            color: #fff;
            padding-top: 1px;
            top: -7px;
            @include mq_min_width($tablet-medium) {
              top: -12px;
            }
            &:before,
            &:after {
              position: absolute;
              top: 50%;
            }
            &:after {
              content: '';
              left: 29px;
              background: $textcolor-2;
              height: 23px;
              width: calc(100% - 20px);
              z-index: -1;
              transform: translateY(-50%) skewX(-25deg);
            }
            &:before {
              @include icomoon;
              letter-spacing: normal;
              font-size: 23px;
              color: $theme-color-2;
              content: '\1f4cd';
              left: 0;
              transform: translateY(-50%);
            }
          }
        }
      }
    }
  }
}

.article-wrapper {
  .article-intro {
    body.article & {
      display: none;
    }
  }
  .article-content {
    font-size: 20px;
    font-weight: 300;
    line-height: 30px;

    > p {
      margin-bottom: 28px;

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .social-sharing {
    display: none;
  }
  body.article & {
    padding-top: 30px;
    .article-title {
      margin: 0;
      .inside {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        text-align: center;
      }
      .at-theme {
        float: none;
        margin: 0;
        background: none;
        padding: 0;
        color: $textcolor-2;
        font-size: 12px;
        letter-spacing: 1.04px;
        font-weight: 500;
        font-family: $font-2;
        & + .at-main-title {
          margin-top: 15px;
        }
      }
      .at-main-title {
        margin: 0 0 28px;
      }
      .at-content {
        order: 1;
        margin: 0;
      }
      img.at-illust,
      img.gla-item-img,
      img.la-item-img,
      img.cx-lazy-load {
        margin: 0;
        order: 2;
        float: none;
        width: auto;
        display: block;
      }
    }
    .article-content {
      @include clearfix;
      margin-top: 51px;
      &:before {
        content: '';
        display: block;
        height: 1px;
      }
      figure {
        float: none;
        border: 0;
        padding: 0;
        text-align: center;
        &.fl {
          margin-right: 0;
          margin-left: 0;
          @include mq_min_width($tablet-medium) {
            float: left;
            margin-right: 20px;
            border: 1px solid #e3e9ea;
            padding: 5px;
            text-align: left;
          }
        }
        &.fr {
          margin-right: 0;
          margin-left: 0;
          @include mq_min_width($tablet-medium) {
            float: right;
            border: 1px solid #e3e9ea;
            margin-left: 20px;
            padding: 5px;
            text-align: left;
          }
        }
      }
    }
  }

  body.article-program & {
    .article-title {
      .topic_hall_b {
        color: #FFF;
        font-family: $font-3;
        font-size: 12px;
        font-weight: 600;
        letter-spacing: 0.89px;
        line-height: 2.16;
        position: relative;
        padding: 0 20px;
        order: 2;

        &::before {
          content: '';
          background: $textcolor-2;
          height: 23px;
          width: 60px;
          z-index: -1;
          -webkit-transform: translateY(-50%) skewX(-25deg);
          -ms-transform: translateY(-50%) skewX(-25deg);
          transform: translateY(-50%) skewX(-25deg);
          position: absolute;
          top: 10px;
          left: calc(50% - 30px);
        }
      }

      .at-content {
        display: flex;
        flex-direction: column;
      }

      .at-main-title {
        color: $textcolor-3;
        font-family: $font-2;
        font-size: 41px;
        font-weight: 900;
        line-height: 63px;
        text-align: center;
        order: 1;
        margin: 0 0 24px;
      }

      p:last-of-type {
        color: $lightgrey-2;
        font-size: 20px;
        font-weight: 600;
        line-height: 1.55;
        text-align: center;
        order: 3;
        margin-bottom: 24px;
      }
      img.at-illust,
      img.gla-item-img,
      img.la-item-img,
      img.cx-lazy-load {
        width: calc(100% - 8.3334%);
        max-width: 823px;

        @include mq_min_width($tablet-medium) {
          width: calc(100% - 33.3334%);
        }
      }
    }

    .article-intro {
      display: block;

      > p {
        display: none;
      }

      .social-sharing {
        display: flex;
        transform: translateY(20px);
        @include mq_min_width($desktop-small) {
          transform: none;
          display: block;
          position: absolute;
          left: 10px;
          border: 0;
          margin-top: 0;
        }
        .inside {
          margin: auto;
          @include mq_min_width($desktop-small) {
            margin: 0;
          }
        }

        .sr-list {
          justify-content: space-evenly;
        }

        .sr-title {
          font-size: 11px;
          @include mq_min_width($desktop-small) {
            font-size: 8px;
          }
          @include mq_min_width($desktop-medium) {
            font-size: 11px;
          }
        }

        .sr-item {
          width: 24px;
          height: 24px;

          @include mq_min_width($desktop-medium) {
            width: 37px;
            height: 37px;
          }

          a::before {
            font-size: 37px;
          }
        }
      }
    }

    .article-content {
      font-size: 16px;
      line-height: 1.56;
      font-weight: 300;
      position: relative;
      margin: 0 -8.3334% 80px;
      @include mq_min_width($tablet-medium) {
        margin-left: 0;
        margin-right: 0;
      }

      .lien2 {
        color: $theme-color-2;
      }
      .lien1 {
        @extend %button;
      }

      p,
      ul {
        padding: 10px 8.3334%;

        @include mq_min_width($tablet-medium) {
          padding: 10px 16.6667%;
        }
      }

      p:nth-of-type(-n+3),
      p:nth-of-type(n+6),
      ul:first-of-type {
        background-color: $lightgrey-3;
        margin: 0;
      }

      p {
        &:first-of-type {
          margin-top: 40px;
        }
        &:nth-of-type(3) {
          padding-bottom: 40px;
        }
        &:nth-of-type(4) {
          margin: 40px 0 0;
        }
        &:nth-of-type(5) {
          margin-top: 0;
        }
        &:nth-of-type(6) {
          padding-top: 40px;

          @include mq_min_width($tablet-medium) {
            padding-left: 30%;
            padding-right: 8.3334%;
          }

          @include mq_min_width(900px) {
            padding-left: 26%;
          }
          @include mq_min_width($desktop-medium) {
            padding-left: 22%;
            padding-top: 40px !important;
          }
        }
        &:nth-of-type(7) {
          a::before {
            content: '\A';
            white-space: pre;
          }
        }

        &:nth-of-type(n+6) {
          font-size: 14px;
          @include mq_min_width($tablet-medium) {
            padding: 10px 8.3334% 10px 30%;
            font-size: 20px;
          }
          @include mq_min_width(900px) {
            padding-left: 26%;
          }
          @include mq_min_width($desktop-medium) {
            padding-left: 22%;
          }
        }

        &:last-of-type {
          padding-bottom: 20px;
          font-weight: 700;

          @include mq_min_width($tablet-medium) {
            padding-bottom: 55px;
          }
        }

        a {
          text-decoration: none;
        }
      }

      &::before {
        background-color: $lightgrey-3;
        height: 150px;
        position: absolute;
        top: -150px;
        width: 100%;
        z-index: -1;

        @include mq_min_width($desktop-small) {
          height: 100px;
          top: -100px;
        }
      }

      ul {
        line-height: 2.312;

        li {
          margin-left: 30px;

          &::before {
            border-radius: 50%;
            background: #000;
          }
        }
      }

      iframe {
        display: block;
        margin: 30px auto 80px;
        max-width: 90%;

        @include mq_min_width($desktop-small) {
          max-width: 66.6667%;
        }
      }

      figure.fl {
        border: 0;
        padding: 30px 20px 0;
        background-color: $lightgrey-3;
        text-align: center;
        margin: 0 auto;
        position: relative;

        &::before {
          width: 100vw;
          height: 100%;
          content: '';
          position: absolute;
          display: block;
          top: 0;
          left: 50%;
          right: 0;
          transform: translateX(-50%);
          background-color: $lightgrey-3;
          z-index: -1;
        }

        @include mq_min_width($tablet-medium) {
          &::before {
            content: none;
          }
          margin: 0;
        }

        @include mq_min_width($desktop-medium) {
          padding: 50px;
        }
      }
    }

    .article-navigation {
      background-color: $lightgrey-3;
      margin: 80px -80px;
      padding: 80px 60px;
      position: relative;

      &::before {
        width: 100vw;
        height: 100%;
        content: '';
        position: absolute;
        display: block;
        top: 0;
        left: 50%;
        right: 0;
        transform: translateX(-50%);
        background-color: $lightgrey-3;
        z-index: -1;
      }

      @include mq_min_width($tablet-medium) {
        margin-left: auto;
        margin-right: auto;
        padding: 80px 0;
      }

      .an-list {
        @include clearfix;
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        flex-direction: column;
        @include mq_min_width($tablet-medium) {
          flex-direction: row;
        }
      }

      .an-item-content {
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 2;
        color: #fff;
        padding: 20px 30px;
        display: flex;
        flex-direction: column;
      }

      .an-item-title {
        font-family: $font-2;
        font-weight: 900;
        font-size: 24px;
        margin-top: 22px;
        margin-bottom: 10px;
      }

      .an-item-link {
        @include clearfix;
        width: 100%;
        height: 100%;
        border-radius: 6px;
        min-height: 200px;
        display: flex;
        align-items: stretch;
        overflow: hidden;

        @include mq_min_width($tablet-medium) {
          min-height: 437px;
        }
      }

      .an-item {
        border-radius: 6px;
        margin: 15px auto;
        height: 100%;
        max-width: 500px;
        box-shadow: 0 4px 9px 0 rgba(0, 0, 0, 0.13);
        width: 98%;
        @include mq_min_width($tablet-medium) {
          width: 48%;
        }

        @include mq_min_width($desktop-small) {
          &:nth-child(odd) {
            margin-left: auto;
            margin-right: 15px;
          }

          &:nth-child(even) {
            margin-right: auto;
            margin-left: 15px;
          }
        }

        img {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
        }

        .icon {
          display: none;
        }
      }

      .an-item-publication {
        display: none;
      }
    }

    .social-sharing {
      background: none;
      padding: 5px;
    }

    .sr-item-txt {
      @include visually-hidden;
    }
  }
}
