@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.block.quicklink-cta,
.block.quicklinks-cta {
  .subheader-navigation & {
    margin: 0 auto;

    @include mq_min_width($desktop-small) {
      float: right;
      width: auto;
      margin: 0 0 0 auto;
    }

    @if $is_mobile {
      margin: 0;
    }
  }

  .ql-item {
    @include mq_min_width($tablet-medium) {
      float: left;
    }

    + .ql-item {
      margin-top: 20px;

      @include mq_min_width($tablet-medium) {
        margin-top: 0;
        margin-left: 20px;
      }
    }

    $btn-list: (
      linkid801347: 'speaker', // commercial contact btn
      linkid801346: 'contact', // Exhibitor subscription btn
      linkid801627: 'wallet', // Commandez votre badge btn
      linkid801628: 'marker', // Plan intéractif btn
      linkid801629: 'matchmaking' // Matchmaking btn
    );

    @each $classname, $glyph in $btn-list {
      &.#{$classname} .btn-primary {
        &::before {
          @include icomoon;
          content: map_get($icon-list, $glyph);
          display: inline-block;
          margin-right: 10px;
          font-size: 27px;
          color: $theme-color-2;
        }
      }
    }
  }

  .btn-primary {
    display: flex;
    padding: 13px 15px;
    font-family: $font-2;
    font-size: 16px;
    font-weight: 900;
    line-height: 27px;
    text-transform: none;
    align-items: center;
    justify-content: center;
    color: $textcolor-2;
    background-color: #FFFFFF;
    border: 0;
    border-radius: 5px;
    box-shadow: 0 4px 9px 0 rgba(#000, .13);

    &::before {
      @include transition(.3s, transform);
    }

    &:hover {
      &:before {
        transform: scale(1.1);
      }
    }
  }
}
