@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.footer-menu {
  .ql-list {
    @include mq_min_width($tablet-medium) {
      column-count: 3;
      overflow: hidden;
      max-height: 202px;
    }
  }

  .ql-item {
    &.linkid794991 { // Infos pratiques
      @include mq_min_width($tablet-medium) {
        margin-bottom: 40px;
      }
    }
    &.linkid794994 { // Média
      @include mq_min_width($tablet-medium) {
        margin-bottom: 40px * 3;
      }
    }
    &.linkid795000 { // Télécharger l'app
      .btn-primary {
        display: flex;
        align-items: center;

        .icon {
          order: 99;
          margin-left: 15px;
        }
      }
    }
  }

  .btn-primary {
    display: block;
    padding: 0;
    font-family: $font-1;
    font-size: 18px;
    font-weight: 500;
    line-height: 40px;
    text-align: left;
    text-transform: none;
    color: $lightgrey-1;
    background: none;
    border: 0;

    &:hover,
    &:focus {
      color: #fff;
    }
  }
}
