@each $name, $glyph in $icon-list {
  .icon.icon-#{$name}::before {
    @include icomoon;
    content: $glyph;
  }
}

/**
 * Other overrides
 */
.cap-icon-shopping_basket::before {
  @include icomoon;
  content: map_get($icon-list, 'star') !important;
}

.icon-apps {
  &::before {
    @include icomoon;
    content: map_get($icon-list, 'apple');
  }

  &::after {
    @include icomoon;
    margin-left: .66em;
    content: map_get($icon-list, 'android');
  }
}
