@import '~bourbon';
@import '../../includes/variables';
@import '../../includes/mixins';

.key-figures {
  clear: both;
  @if not $is_mobile {
    max-width: 1100px;
  }
  margin: 30px auto;
  @include mq_min_width($tablet-medium) {
    margin: 40px auto;
  }
  @include mq_min_width($desktop-medium) {
    margin: 80px auto;
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    position: relative;
    width: 100%;
    padding-left: 91px;
    margin-bottom: 17px;

    @include mq_min_width($tablet-medium) {
      width: 50%;
    }

    @include mq_min_width($desktop-small) {
      width: 25%;
      padding-left: 85px;
    }

    .is-ready & {
      opacity: 0;
    }

    .is-ready.is-in-viewport & { // animation
      animation: slideUpFadeIn 1.2s forwards;

      @for $i from 1 through 4 {
        &:nth-child(#{$i}) {
          animation-delay: .1s * ($i - 1);
        }
      }
    }

    &__icon {
      position: absolute;
      left: 0;
      top: 0;
      float: left;
      display: block;
      width: 70px;
      height: 70px !important;
      margin-right: 21px;
      color: #fff;
      background: $theme-color-1;
      border-radius: 50%;

      @include mq_min_width($tablet-medium) {
        margin-right: 15px;
      }
    }

    &__title {
      font-family: $font-2;
      font-size: 25px;
      font-weight: 900;
      line-height: 40px;
      color: $textcolor-4;
    }

    &__text {
      font-family: $font-1-alt;
      font-size: 18px;
      font-weight: 300;
      line-height: 31px;
      color: $textcolor-4;
    }
  }
}
