@import '../../includes/variables';
@import '../../includes/mixins';

.hero {
  position: relative;
  display: flex;
  height: 335px;
  padding-bottom: 45px;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @include mq_min_width($tablet-medium) {
    height: 660px;
  }

  &__wrapper {
    margin: auto;
  }

  &__title {
    font-family: $font-2;
    font-weight: 300;
    text-align: center;
    margin-top: -132.5px;
    font-size: 27px;
    max-width: calc(100% - 40px);
    line-height: 36px;
    @include mq_min_width($tablet-small) {
      font-size: 33px;
      line-height: 42px;
      max-width: 480px;
    }
    @include mq_min_width($tablet-medium) {
      font-size: 50px;
      line-height: 63px;
      max-width: 692px;
      margin-top: -175px;
    }
    span {
      display: block;
      font-weight: 900;
    }
    b, strong {
      font-weight: 800;
    }
  }

  &__img__container {
    position: absolute;
    top: 0;
    left: 50%;
    display: block;
    width: 100vw;
    height: 100%;
    overflow: hidden;
    transform: translateX(-50%);
    z-index: -1;
  }

  &__slider__wrapper {
    height: 100%;
    display: flex;
    max-width: none;
    overflow: visible;
  }

  &__slider__img {
    background: center center / cover no-repeat;
    height: 100%;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    &.txt_white {
      .hero__title {
        color: #fff;
      }
    }
    &.txt_dark {
      .hero__title {
        color: $textcolor-2;
      }
    }
  }

  &__counter {
    display: flex;
    width: 152px;
    justify-content: center;
    flex-wrap: wrap;
    margin: 132.5px auto auto;
    @include mq_min_width($tablet-medium) {
      width: 255px;
      margin: 175px auto auto;
    }

    &__item {
      float: left;
      width: 31px;
      text-align: center;

      @include mq_min_width($tablet-medium) {
        width: 52px;
      }

      & + & {
        margin-left: 9px;

        @include mq_min_width($tablet-medium) {
          margin-left: 15px;
        }
      }
    }

    &__name {
      margin-bottom: 5px;
      font-family: $font-2;
      font-size: 7.5px;
      font-weight: 900;
      text-align: center;
      text-transform: uppercase;
      transition: color 0.3s ease-in-out;
      @include mq_min_width($tablet-medium) {
        font-size: 13px;
      }
    }

    &__number {
      height: 31px;
      font-family: $font-2;
      font-size: 15px;
      font-weight: 900;
      line-height: 31px;
      text-align: center;
      color: $lightgrey-1;
      background-color: #fff;
      border-radius: 4px;

      @include mq_min_width($tablet-medium) {
        height: 52px;
        font-size: 26px;
        line-height: 52px;
      }
    }

    &__progressbar {
      position: relative;
      width: 100%;
      height: 2px;
      margin-top: 30px;
      background: #fff;

      @include mq_min_width($tablet-medium) {
        margin-top: 50px;
      }

      &__inner {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        width: 0;
        background: $theme-color-2;
        //@include transition(1s, width);
      }
    }
  }
  &.no-counter {
    .hero__counter {
      margin-top: 100px;
      @include mq_min_width($tablet-medium) {
        margin-top: 100px;
      }
    }
    .hero__title {
      margin-top: -100px;
      @include mq_min_width($tablet-medium) {
        margin-top: -100px;
      }
    }
  }
  &.txt_white {
    .hero__counter__name {
      color: #fff;
    }
  }
  &.txt_dark {
    .hero__counter__name {
      color: $textcolor-2;
    }
  }
  &:not(.is-ready) {
    .hero__slider__img {
      width: 100%;
    }
  }
}
